import Article from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'article',
    path:'article',
    meta:{alias:'文章'},
    component:Article,
    redirect:{name:'article-grid'},
    subRoutes:[
        {
            name:'article-grid',
            path:'grid',
            meta:{alias:'表格',isForm:false,moduleDesc:'文章',dataDesc:''},
            component:Grid
        },
        {
            name:'article-form',
            path:'form',
            meta:{alias:'表格',isForm:true},
            component:Form
        }
    ]
}


