<template>
    <el-dialog title="提供单位坐标信息" :visible="true" :before-close="cancel" @open="initMap">
        <div id="map-content" :class="style.layout">

        </div>
        <span slot="footer" class="dialog-footer" v-if="allowPickup">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="save">确 定</el-button>
        </span>
    </el-dialog>
</template>
<script>
import style from '@/css/point-map-pickup.css'
export default {
    name: 'point-pickup',

    data() {
        return {
            style,
            location: null,
            map: null,
            marker: null,
        }
    },
    props: {
        point: null,
        allowPickup: true,
    },
    methods: {
        //地图相关
        initMap() {
            let baseVecLayer = new L.TileLayer.WMTS("http://t0.tianditu.com/vec_c/wmts", {
                tileSize: 256,
                layer: 'vec',
                style: "default",
                tilematrixSet: "c",
                format: "tile",
            });
            let baseCvaLayer = new L.TileLayer.WMTS("http://t0.tianditu.com/cva_c/wmts", {
                tileSize: 256,
                layer: 'cva',
                style: "default",
                tilematrixSet: "c",
                format: "tile",

            });
            let latlng = L.latLng(40, 120);
            this.map = L.map('map-content', { center: latlng, zoom: 3, crs: L.CRS.EPSG4326, layers: [baseVecLayer, baseCvaLayer] });
            if (this.allowPickup == true) {

                this.map.on("click", this.mapClick);

            }
            if (this.point != null) {
                this.location = new L.LatLng(this.point.split(',')[1], this.point.split(',')[0]);
                this.addMarkers();
                this.map.flyTo(this.location);
            }
        },

        mapClick(ev) {
                this.location = ev.latlng;
                this.addMarkers();
        },

        addMarkers() {
            if (this.marker != null)
                this.map.removeLayer(this.marker);
            let myIcon = L.icon({ iconUrl: 'static/assets/icons/marker-icon.png', iconSize: [30, 30], popupAnchor: [-3, -76], });
            this.marker = L.marker(this.location, { icon: myIcon });
            this.map.addLayer(this.marker);
        },

        // 界面相关
        cancel() {
            this.$emit('cancel');
        },
        save() {
            let coordinateData = this.formatDegreeBack(this.location.lat, 'lat') + ',' + this.formatDegreeBack(this.location.lng, 'lon');

            //转为度分秒格式保存
            this.$emit('save', coordinateData);
        },

    },
    components: {

    },
    watch: {
        point() {

        },

    },
    created() {
    },
    mounted() {
        this.$nextTick(function() {
            this.initMap();
        })



    }
}
</script>


