<template>
  <div :class="style.bread">
    <div :class="style.home">
      <i :class="style.homeIcon"></i>
    </div>

    <el-breadcrumb separator=">" :class="style.breadItem">
      <el-breadcrumb-item v-for="(item,index)  in breadcrumbList" :key="`${index}`">
        <router-link v-if='item.redirect==="noredirect"||index==breadcrumbList.length-1' to="" :class="style.noRedirect">{{item.meta.alias}}</router-link>
        <router-link v-else :to="item.redirect||item.path">{{item.meta.alias}}</router-link>
      </el-breadcrumb-item>
    </el-breadcrumb>
    <back-button :class="style.backBtn" v-if="isForm"></back-button>
  </div>
</template>
<script>
import style from '@/css/breadcrumb.css'
import BackButton from '#com/back-button'
export default {
  name: 'breadcrumb',
  created() {
    this.getBreadcrumb()
  },
  data() {
    return {
      style,
      breadcrumbList: null,
      isForm: false,
    }
  },
  methods: {
    getBreadcrumb() {
      this.breadcrumbList = this.$route.matched;
      // let lastRout = this.breadcrumbList[this.breadcrumbList.length - 1];
      let lastRout = this.$route;
      this.isForm = false;
      if (lastRout.meta.isForm === true) {
        this.isForm = true;
        lastRout.meta.alias = lastRout.query.state == "add" ? "新建" : lastRout.query.name;
      }

    }
  },
  components: {
    BackButton
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    }
  }
}
</script>


