import { sysClient } from '@/clients/hprose-client'
import * as Types from './mutation-types'
import QueryParam from '@/models/business/query-param'

const state = {
    queryParam: new QueryParam()
}

const getters = {
    [Types.queryParam]: ({ queryParam }) => queryParam
}

const mutations = {
    [Types.SET_QUERYPARAM]: (state, queryParam) => state.queryParam = queryParam
}
const actions = {
    [Types.addRecords]: (context, args) => sysClient.getData('addVideo', args, true),
    [Types.getRecords]: (context, args) => sysClient.getData('getVideoList', args, false),
    [Types.editRecords]: (context, args)=>sysClient.getData('editVideoInfo', args, true),
    [Types.getCompanyData]: (context,args)=>sysClient.getData('getCompanyList', args, false),
    [Types.getLanguageList]: (context,args)=>sysClient.getData('getLanguage', args, false),
    [Types.getPeriodList]:(context,args)=>sysClient.getData('getPeriod', args, false),
    [Types.setSpeList]:(context,args)=>sysClient.getData('setSpeList', args, false),
    [Types.readRecord]:(context,args)=>sysClient.getData('getVideoDetail',args,false),
    [Types.delData]: (context, args) => {
        //删除或批量删除
        return sysClient.getData('dropVideoData', args,true);
    },
     [Types.getEnclPath]:(context,args)=>sysClient.getData('getEnclPath',args,false),
     [Types.publishData]:(context,args)=>sysClient.getData('PublishVideoData',args,true),
     [Types.exportData]:(context,args)=>sysClient.getData('videoExport',args,true),
     [Types.importData]:(context,args)=>sysClient.getData('videoImport',args,true),
}
export default {
    state,
    getters,
    mutations,
    actions
}


