<template>
  <div :class="style.layout">
    <span :class="style.title"></span>
    <div :class="style.exitDiv">
      <i :class="style.exit" @click="exit"></i>
      <span :class="style.textCommon">欢迎，{{username}}</span>
    </div>
    <!-- <span :class="style.weather">
            <iframe width="180" scrolling="no" height="18" frameborder="0" allowtransparency="true" src="http://i.tianqi.com/index.php?c=code&id=1&color=%23FFFFFF&icon=1&py=pingyao&wind=0&num=1&site=12"></iframe>
          </span> 
          <span :class="style.textCommon">{{dateStr}}</span> -->
  </div>
</template>

<script>
import style from '@/css/header.css'
import { SET_HASLOINGED } from '../modules/login/mutation-types'
import { mapMutations, mapActions,mapGetters } from 'vuex'
import { exitSession, user } from '@/components/modules/login/mutation-types'

export default {
  name: 'app-header',
  data() {
    return {
      style,
      username: '超级用户',
      date: new Date(),
    }
  },
  computed: {
    dateStr() {
      let year = this.date.getFullYear();
      let month = this.date.getMonth() + 1;
      let day = this.date.getDate();
      let hour = this.date.getHours();
      let minitue = this.date.getMinutes();
      let second = this.date.getSeconds();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      if (hour < 10) {
        hour = "0" + hour;
      }
      if (minitue < 10) {
        minitue = "0" + minitue;
      }
      if (second < 10) {
        second = "0" + second;
      }
      return `${year}-${month}-${day} ${hour}:${minitue}:${second}`
    },
    ...mapGetters({
      user
    })
  },

  methods: {
    ...mapActions({
      exitSession
    }),
    ...mapMutations({
      SET_HASLOINGED
    }),
    dateChange() {
      setInterval(() => {
        this.date = new Date();
      }, 1000);
    },
    exit() {
      this.exitSession();
      this.SET_HASLOINGED(false);
      this.$router.push({ name: "login" });
    }
  },

  created() {
    this.dateChange();
  },
  mounted() {
    if (localStorage.getItem('realname')) {
      this.username = localStorage.getItem('realname');
    }
  }
}
</script>


