<template>
    <div>
        <common-grid :tableData="tableData" :collums="collums" :total="total" @search="changeQueryParam" @read="viewDetail" @edit="edit" @add="add" 
        @del="delDatas" @toSpecial="toSpecialItem" @publish="publish"  @downLoad="downLoad" @exportData="exportDatafun" @uploadSuccessful="uploadSuccessful">
        </common-grid>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { getRecords } from './mutation-types'
import { delData ,signMapData,publishData,getEnclPath,exportData,importData} from './mutation-types'
import collums from '@/data/map-grid-header'
import CommonGrid from '#com/common-grid'
import QueryParam from '@/models/business/query-param'
import FormRouteQuery from '@/models/business/form-route-query'
import OM_MAP from '@/models/entity/OM_MAP'
import * as  EditState from '@/enums/edit-state'
import { DOWNLOADURL } from '@/configs/upload-download'
export default {
    name: 'atlas-grid',
    data() {
        return {
            tableData: null,
            collums,
            total: 0,
            queryParam: new QueryParam(),
            formId: ""
        }
    },
    methods: {
        changeQueryParam(param) {
            this.queryParam = param;
            this.getData();
        },
          //导出
        async exportDatafun(expdata,keyword) {
            let result = await this.exportData([JSON.stringify(expdata),keyword]);
            var a = document.createElement('a'); 
            a.download = "";
            a.style.display = 'none';
            a.href = DOWNLOADURL+result;
            a.click();
        },
        async getData() {
            let result = await this.getRecords(Object.values(this.queryParam));
            if (!result.list) {
                this.tableData = [];
            } else {
                let data = result.list;
                data.forEach(function (item) {
                    if (item.SZGJ) {
                        item.SZZGJ = item.SZZ + ',' + item.SZGJ;
                    }else{
                        item.SZZGJ = item.SZZ;
                    }

                });
                this.tableData = data;
            }
            this.total = result.count;
        },
          //导入附件上传成功后回调
       async uploadSuccessful(load){
             let result = await this.importData(load);
             this.$message({
                showClose: true,
                message: result.result,
                type:result.status?'success':'error',
            });
            if (result.status) this.getData();
        },
        ...mapActions({
            getRecords,
            delData,
            signMapData,
            publishData,
            getEnclPath,
            exportData,
            importData
        }),
        viewDetail(index, row) {
            let route = { name: "atlas-form", query: new FormRouteQuery(row.ID, EditState.READ, row.YYMC) };
            this.$router.push(route);
        },
        edit(index, row) {
            let route = { name: "atlas-form", query: new FormRouteQuery(row.ID, EditState.EDIT, row.YYMC) };
            this.$router.push(route);
        },
        add() {
            let route = { name: "atlas-form", query: new FormRouteQuery(null, EditState.ADD, "") };
            this.$router.push(route);
        },
        async delDatas(datas) {
            //此处调用删除数据逻辑，datas为删除或批量删除的数据数组
            let result = await this.delData(JSON.stringify(datas));
            
            this.$message({
                showClose: true,
                message: result.result,
            });

            if (result.status === true) {
                this.getData();
            }
        },
        async toSpecialItem(param) {
            let result = await this.signMapData(param);
            this.$message({
                showClose: true,
                message: result,
            });
            if (result) this.getData();

        },
         async publish(vidData,isTrue){
            let result = await this.publishData([JSON.stringify(vidData),isTrue]);
           this.$message({
                showClose: true,
                message: result.result,
                type:result.status?'success':'error',
            });
            if (result) this.getData();
        },      
        async downLoad(id) {
            let result = await this.getEnclPath(id);
            if (!result || !result.count) { alert("暂无附件"); return; };
            let files = result.list;
            files.forEach(function(element) {
                this.downLoadFile(element.FJLJ);
            }, this);
        },
        downLoadFile(filename) {
            let filepath=DOWNLOADURL+filename;
            var a = document.createElement('a');
            a.download = "";
            a.style.display = 'none';
            a.href = filepath;// 模拟a标签点击进行下载
            a.click();
        }, 
    },
    components: {
        CommonGrid
    },

    created() {
        this.getData();
    },

}
</script>




