import main from './'
import Map from '#mod/map/router'
import Atlas from '#mod/atlas/router'
import Article from '#mod/article/router'
import Image from '#mod/image/router'
import Video from '#mod/video/router'
import Treatise from '#mod/treatise/router'
import User from '#mod/user/router'
export default {
    name:'main',
    meta:{alias:'主页'},
    path:'main',
    component:main,
    redirect:{name:'map'},
    subRoutes:[
        Map,
        Atlas,
        Article,
        Image,
        Video,
        Treatise,
        User
    ]
}


