<template>
  <div :class="style.menu">
    <el-menu :default-active="currentActive" router :unique-opened="true" class="el-menu-vertical-demo">
      <template v-for="(item,index) in menuData">
        <el-submenu v-if="item.children" :key="index" :index="`${index}`">
          <template slot="title">
            <div :class="style.icon">
              <i :class="item.icon"></i>
            </div>
            <span class="menutitle">{{item.label}}</span>
          </template>
          <el-menu-item v-for="(subItem,subIndex) in item.children" :key="index+'-'+subIndex" :index="index+'-'+subIndex" :route="{name:subItem.route}">
            <span class="menucontent">{{subItem.label}}</span>
          </el-menu-item>
        </el-submenu>
        <el-menu-item v-else :key="index" :index="`${index}`" :route="{name:item.route}" class="menubackgroud">
          <div :class="style.icon">
            <i :class="item.icon"></i>
          </div>
          <span class="menutitle">{{item.label}}</span>
        </el-menu-item>
      </template>
    </el-menu>
  </div>
</template>
<script>
import style from '@/css/menu.css'
import Data from '@/data/menu'
import { user } from '@/components/modules/login/mutation-types'
import { mapGetters } from 'vuex'
export default {
  name: 'app-menu',
  data() {
    return {
      style,
      menuData:null,
      currentActive: '0-0',
    }
  },
  created() {
    let dataJsonStr = JSON.stringify(Data);
    this.menuData = JSON.parse(dataJsonStr);
    if (this.user && this.user.ROLEID && this.user.ROLEID == "2") {
        //如果是普通用户,不显示菜单
        this.menuData.pop();
      }
    let str = this.$route.name;
    let arr = str.split('-');
    let index = '';
    this.menuData.forEach(function(item) {
      if (item.hasOwnProperty('children')) {
        item.children.forEach(function(item1) {
          if (item1.route == arr[0]) {
            index = item1.index;
          }
        });
      }
      if (item.route == arr[0]) {
        index = item.index;
      }
    });
    this.currentActive = index;
    
  },
  computed: {
    ...mapGetters({
      user
    })
  },
}
</script>


