<template>
    <el-dialog class="author" title="作者" :visible="authorDialogVisible" :before-close="cancel" size="tiny">
        <div :index="index" :item="item" v-for="(item,index) in formData" :key="`${index}`" @del="del" class="flex-container">
            <span style="width:60px">作者：</span>
            <el-input v-model="item.ZZ"></el-input>
            <i class="el-icon-minus" @click="del(index)" v-if="autherFooterVisible"></i>
            <i class="el-icon-plus" @click="add" v-if="(index===formData.length-1)&&autherFooterVisible"></i>
            <i style="padding:12px" v-else></i>
        </div>
        <i class="el-icon-plus" @click="add" style=" float: left;margain-left:0px" v-if="!formData.length"> 新增作者</i>
        <div slot="footer" class="dialog-footer" v-if="autherFooterVisible">
            <el-button type="primary" @click="save">确 定</el-button>
            <el-button @click="cancel">取 消</el-button>
        </div>
    </el-dialog>
</template>
<script>
export default {
    data() {
        return {
            formData: [{ ZZ: '' }],
        }
    },
    watch: {
        authorData() {
            let arr = this.authorMsgStr2Array(this.authorData);
            this.formData = arr;

        }
    },
    props: {
        authorDialogVisible: Boolean,
        authorData: String,
        autherFooterVisible: Boolean
    },
    methods: {
        authorMsgStr2Array(str) {
            let result = [];
            let msgArr = [];
            if (!str) return result;

            if (str.indexOf(',') <= 0) {
                msgArr.push(str);
            } else {
                msgArr = str.split(",");
            }

            msgArr.forEach(function(item) {
                result.push({ ZZ: item });
            });
            return result;
        },
        add() {
            this.formData.push({ ZZ: '' });
        },
        del(index) {
            let temp = this.formData;
            temp.splice(index, 1);
            this.formData = temp;
        },
        save() {
            //组织成字符串
            let msg = "";
            this.formData.forEach(function(item) {
                msg += item.ZZ + ',';
            });
            msg = msg.slice(0, msg.length - 1);
            this.$emit('save', msg);
        },
        cancel() {
            this.$emit('cancel');
        }
    }
}
</script>




