import Map from './'
import Form from './form'
import Grid from './grid'
export default {
    name: 'map',
    meta:{alias:'地图'},
    path: 'map',
    component: Map,
    redirect:{name:'map-grid'},
    subRoutes: [
        {
            name: 'map-form',
            path: 'form',
            meta:{alias:'表单',isForm:true},
            component: Form,
        },{
            name:'map-grid',
            path:'grid',
            meta:{alias:'表格',isForm:false,moduleDesc:'地图',dataDesc:'遗址信息'},
            component:Grid
        }
    ]
}


