<template>
    <div>
        <el-input placeholder="请输入内容" v-model="address" @change="change" class="el-input-template">
            <template slot="prepend">{{url}}</template>
        </el-input>
        <i class="el-icon-delete2" @click="del(index)"></i>
    </div>
</template>
<script>
export default {
    name: 'file-Dialog',
    data() {
        return {
            address: '',
        }
    },
    model: {
        prop: 'address',
        event: 'change'
    },
    props: {        
        index: 0,
        fileName: '',
        url:''
    },
    methods: {
        change(){
            this.$emit('change',this.address);
        },
        del(index) {
            this.$emit('del', index);
        },
        
    },
    created() {
        // this.url = `${this.fileName}/${this.getNowFormatDate()}/`;
    }
}
</script>



