import Vue from 'vue';
import Vuex from 'vuex';
import login from '#mod/login/store'
import map from '#mod/map/store'
import atlas from '#mod/atlas/store'
import article from '#mod/article/store'
import image from '#mod/image/store'
import treatise from '#mod/treatise/store'
import video from '#mod/video/store'
import user from '#mod/user/store'
Vue.use(Vuex);
import {
	Promise
} from 'es6-promise'
window.Promise = Promise;

export default new Vuex.Store({
	modules:{
		login,
		map,
		atlas,
		article,
		image,
		treatise,
		video,
		user

	}
});


