<template>
    <div :class="style.user" @keyup.13="gologin">
        <div :class="style.username">
            <div>
                <img :class="style.img" src="../../../../static/assets/login/username.png" />
            </div>
            <input :class="style.loginInput" v-model="userName" type="text" placeholder="请输入用户账号">
        </div>
        <div :class="style.password">
            <div>
                <img :class="style.img" src="../../../../static/assets/login/password.png" />
            </div>
            <input :class="style.loginInput" v-model="passWord" type="password" placeholder="请输入密码">
        </div>
        <div :class="style.identifycode">
            <div :class="style.identify">
                <img :class="style.img" src="../../../../static/assets/login/yanz.png" />
            </div>
            <input :class="style.loginInput" v-model="identifyingCode" type="text" placeholder="验证码">
            <login-checkcode :class="style.checkcode" @changed="changeCode"></login-checkcode>
        </div>
        <!-- <div :class="style.remember">
                    <el-checkbox>记住密码</el-checkbox>
                </div> -->
        <p :class="style.errorMessage" v-text="message"></p>
        <a :class="style.login" v-on:click="gologin">
           登 录
        </a>
    </div>
</template>
<script>
let codeMessage;
let inp = document.getElementsByTagName('input');
import style from '@/css/login-form.css'
import { login, getSession, SET_USER, SET_HASLOINGED, user} from './mutation-types'
import { mapActions, mapMutations,mapGetters } from 'vuex'
import LoginCheckcode from './login-checkcode'
export default {
    name: 'log-form',
    data() {
        return {
            style,
            userName: "",
            passWord: "",
            identifyingCode: "",
            yzcode: "",
            message: "",
        }

    },
    components: {
        LoginCheckcode
    },
    methods: {
        // async loginBtn(event) {
        //  let data = await this.login([this.userName, this.passWord]);
        // }
        async gologin() {
            let pass = this.checkUserForm();
            if (!pass) return;
            this.saveNameinLocal();
            //登录服务
            let result = await this.login([this.userName, this.passWord]);
            if (!result.status) return this.handleLoginFailed();
            this.SET_USER(result.result);

            if(result.result.REALNAME!=null){
                localStorage.setItem('realname', result.result.REALNAME);
            }
            //跳转路由
            this.$router.push({ name: "main" });
            //设置登录状态
            this.SET_HASLOINGED(true);
            //计时去获取session，不用重复登录
            this.checkSessionTimeout();
        },

        checkUserForm() {
            if (!this.userName) {
                this.message = "请输入用户名！";
                return false;
            }
            if (!this.passWord) {
                this.message = "请输入密码！"
                return false;
            }
            if (!this.identifyingCode || this.codeMessage != this.identifyingCode.toLowerCase()) {
                this.message = "验证码错误！";
                return false;
            }
            return true;
        },


        checkSessionTimeout() {
            window.setTimeout(this.resetHasLogined4TimeOut, 2400000);
        },

        async resetHasLogined4TimeOut() {
            let result = await this.getSession();
            if (!result) return this.SET_HASLOINGED(fasle);
            this.checkSessionTimeout();
        },

        handleLoginFailed() {
            this.message = "用户或密码错误！";
        },
        ...mapActions({
            login,
            getSession
        }),
        ...mapMutations({
            SET_HASLOINGED,
            SET_USER
        }),
        changeCode(code) {
            this.codeMessage = code;
        },
        //将用户名保存到local localStorage        
        saveNameinLocal() {
            var n = inp[0].value;
            localStorage.setItem('name', n);
        },
        //登陆过记录登录名
        showName() {
            if (localStorage.getItem('name')) {
                this.userName = localStorage.getItem('name');
            }
        }
    },

    created() {
        // this.getSession().then(result => {
        //     console.log(777,result);
        //     if(result){
        //         this.$router.push({name:"main"})
        //     }else
        //         this.$router.push({name:"login"})
        // })              
    },
    mounted() {
        this.showName(); 
    },
    computed: {
        ...mapGetters({
            user
        }),

    }
}
</script>




