<template>
    <div>
        <!-- <excel-export></excel-export> -->
        <common-grid :tableData="tableData" :collums="collums" :total="total" @read="viewDetail" @add="add" @search="changeQueryParam" @del="delDatas" @edit="edit" @toSpecial="toSpecialItem"
         @downLoad="downLoad" @publish="publish" @exportData="exportDatafun" @uploadSuccessful="uploadSuccessful">
        </common-grid>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { getRecords, delData, setSpeList,getEnclPath,publishData,exportData,importData } from './mutation-types'
// import { delData } from './mutation-types'
import collums from '@/data/video-grid-header'
import CommonGrid from '#com/common-grid'
import ExcelExport from '#com/excel-export'
import QueryParam from '@/models/business/query-param'
import * as  EditState from '@/enums/edit-state'
import FormRouteQuery from '@/models/business/form-route-query'
import { DOWNLOADURL } from '@/configs/upload-download'
export default {
    name: 'video-grid',
    data() {
        return {
            tableData: null,
            collums,
            total: 0,
            queryParam: new QueryParam()
        }
    },
    methods: {
        viewDetail(index, row) {
            this.$router.push({
                name: 'video-form',
                query: new FormRouteQuery(row.ID, EditState.READ, row.SPMC)
            })
        },
        changeQueryParam(param) {
            this.queryParam = param;
            this.getData();
        },
        //导出
        async exportDatafun(expdata,keyword) {
            let result = await this.exportData([JSON.stringify(expdata),keyword]);
            var a = document.createElement('a');
            a.download = "";
            a.style.display = 'none';
            a.href =DOWNLOADURL+result;
            a.click();
        },
        async getData() {
            let result = await this.getRecords(Object.values(this.queryParam));
            this.tableData = result.list;
            this.total = result.count;
        },
          //导入附件上传成功后回调
       async uploadSuccessful(load){
             let result = await this.importData(load);
             this.$message({
                showClose: true,
                message: result.result,
                type:result.status?'success':'error',
            });
            if (result.status) this.getData();
        },
        ...mapActions({
            getRecords,
            delData,
            setSpeList,
            getEnclPath,
            publishData,
            exportData,
            importData
        }),
        add() {
            this.$router.push({
                name: 'video-form',
                query: { state: EditState.ADD }
            });
        },
        async delDatas(datas) {
            //此处调用删除数据逻辑，datas为删除或批量删除的数据数组
            let result = await this.delData(JSON.stringify(datas));

            this.$message({
                showClose: true,
                message: result,
            });

            if (result.status === true) {
                this.getData();
            }
        },
        async toSpecialItem(param) {
            let result = await this.setSpeList(param);
            this.$message({
                showClose: true,
                message: result,
            });
            if (result) this.getData();

        },
        //编辑
        edit(index, row) {
            this.$router.push({
                name: 'video-form',
                query: new FormRouteQuery(row.ID, EditState.EDIT, row.SPMC)
            });
        },
       async publish(vidData,isTrue){
            let result = await this.publishData([JSON.stringify(vidData),isTrue]);
           this.$message({
                showClose: true,
                message: result.result,
                type:result.status?'success':'error',
            });
            if (result) this.getData();
        },       
        async downLoad(id) {
            let result = await this.getEnclPath(id);
            if (!result || !result.count) { alert("暂无附件"); return; };
            let files = result.list;
            files.forEach(function(element) {
                this.downLoadFile(element.FJLJ);
            }, this);
            
        },
        downLoadFile(filename) {
            let filepath=DOWNLOADURL+filename;
            // window.location.href=filepath;

            // var ifr = document.createElement('iframe');
            // ifr.style.display = 'none';
            // ifr.src = filepath;
            // document.body.appendChild(ifr);
            
            // console.log(1221, filename,ifr);
            // // 保存页面 -> 保存文件
            // ifr.contentWindow.document.execCommand('SaveAs',filename, "E:\protects\OceanMap\frontend\oceanmap\file");
         //  document.body.removeChild(ifr);   

            // var a = document.createElement('a');
            // a.download=filename;
            // a.href='http://gphp.geo-compass.com/oceanmap/data/zhanlang.txt';
            // console.log(1221,a);
            // a.click();

            // var canvas = document.getElementById(filepath);
            // canvas.toBlob(function(blob){// 使用 createObjectURL 生成地址，格式为 blob:null/fd95b806-db11-4f98-b2ce-5eb16b38ba36
            // var url = URL.createObjectURL(blob);

            var a = document.createElement('a');
            a.download = "";
            a.style.display = 'none';
            a.href = filepath;// 模拟a标签点击进行下载
            a.click();// 下载后告诉浏览器不再需要保持这个文件的引用了
          //  URL.revokeObjectURL(url);});


        }
    },
    components: {
        CommonGrid,
        ExcelExport
    },

    created() {
        this.getData();
    }
}
</script>




