<template>
    <el-dialog title="提供单位坐标" :visible="dialogFormVisible" :before-close="cancel">
        <map-dialog :index="index" :item="item" v-for="(item,index) in formData" :key="`${index}`" @del="del" :mapCoorFooterVisible="false"></map-dialog>
        <div slot="footer" class="dialog-footer" v-if="mapCoorFooterVisible">
            <el-button type="primary" @click="save">确 定</el-button>
            <el-button @click="cancel">取 消</el-button>
        </div>
    </el-dialog>
</template>
<script>
import LAT from '@/models/business/LAT'
import LONG from '@/models/business/LONG'
import MapDialog from '#com/map-dialog'
export default {
    name: 'company-Coordinate',
    data() {
        return {
            formData: [{ lat: new LAT({degree:'0',minutes:'0',seconds:'0',latitude:'N'}), long: new LONG({degree:'0',minutes:'0',seconds:'0',longitude:'E'}) }],
            item: null,
            index: 0
        }
    },
    watch: {
        mapCoordinateData() {
            if (!this.mapCoordinateData) return;
            let arr = this.mapLocationStr2Array(this.mapCoordinateData);
            this.formData = arr;
        }
    },
    props: {
        dialogFormVisible: Boolean,
        mapCoordinateData: String,
        mapCoorFooterVisible: Boolean
    },
    methods: {
        mapLocationStr2Array(locStr) {
            if (!locStr) return;
            let locArr = [];
            locArr.push(locStr);
            let itemArr;
            let objLat = new LAT();
            let objLong = new LONG();
            let result = [];
            locArr.forEach(function(item) {
                itemArr = item.split(",");
                objLat.degree = itemArr[0].slice(0, itemArr[0].indexOf('°'));
                objLat.minutes = itemArr[0].slice(itemArr[0].indexOf('°') + 1, itemArr[0].indexOf('′'));
                objLat.seconds = itemArr[0].slice(itemArr[0].indexOf('′') + 1, itemArr[0].indexOf('″'));
                objLat.latitude = itemArr[0].slice(itemArr[0].indexOf('″') + 1, itemArr[0].length);

                objLong.degree = itemArr[1].slice(0, itemArr[1].indexOf('°'));
                objLong.minutes = itemArr[1].slice(itemArr[1].indexOf('°') + 1, itemArr[1].indexOf('′'));
                objLong.seconds = itemArr[1].slice(itemArr[1].indexOf('′') + 1, itemArr[1].indexOf('″'));
                objLong.longitude = itemArr[1].slice(itemArr[1].indexOf('″') + 1, itemArr[1].length);


                result.push({ lat: new LAT(objLat), long: new LONG(objLong) });
            });
            return result;
        },
        add() {
            this.formData.push({ lat: new LAT({degree:'0',minutes:'0',seconds:'0',latitude:'N'}), long: new LONG({degree:'0',minutes:'0',seconds:'0',longitude:'E'}) });
        },
        del(index) {
            let temp = this.formData;
            temp.splice(index, 1);
            this.formData = temp;
        },
        save() {
            //组织成字符串
            let coordinateData = "";
            this.formData.forEach(function(item) {
                coordinateData += item.lat.degree + '°' + item.lat.minutes + '′' + item.lat.seconds + "″" + item.lat.latitude + ',' + item.long.degree + '°' + item.long.minutes + '′' + item.long.seconds + '″' + item.long.longitude + ';';
            });
            this.$emit('save', coordinateData);
        },
        cancel() {
            this.$emit('cancel');
        }
    },
    // created() {
    //     this.formData.push({ lat: new LAT(), long: new LONG() });
    // },
    components: {
        MapDialog
    }
}
</script>




