<template>
    <el-pagination @size-change="sizeChange" @current-change="currentChange" :current-page="page.pageNum" :page-sizes="page.pageSizes" :page-size="page.pageSize" :layout="page.layout" :total="total">
    </el-pagination>
</template>
<script>
export default {
    name: "common-pagination",
    data() {
        return {
            page: {
                pageNum: 1,
                pageSize: 15,
                pageSizes: [15, 30, 45, 60],
                layout: "total, sizes, prev, pager, next, jumper",
            },
        }
    },
    props: {
       param:{},
       total:0
    },
    computed: {
        params() {
            let {pageNum,pageSize} = this.page;
            return Object.assign({},this.param,{pageNum,pageSize});
        }
    },
    methods: {
        sizeChange(val) {
            this.page.pageSize = val;
            this.$emit('pageChange',this.params);
        },
        currentChange(val) {
            this.page.pageNum = val;
            this.$emit('pageChange',this.params);
        },
    }
}
</script>


