import Image from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'image',
    path:'image',
    meta:{alias:'影像'},
    component:Image,
    redirect:{name:'image-grid'},
    subRoutes:[
        {
            name:'image-grid',
            path:'grid',
            meta:{alias:'表格',isForm:false,moduleDesc:'影像',dataDesc:''},
            component:Grid
        },
        {
            name:'image-form',
            path:'form',
            meta:{alias:'表单',isForm:true},
            component:Form
        }
    ]
}


