import Atlas from './'
import Grid from './grid'
import Form from './form'
export default {
    name: 'atlas',
    meta: { alias: '地图集' },
    path: 'atlas',
    component: Atlas,
    redirect: {name:'atlas-grid'},
    subRoutes: [
        {
            name:'atlas-grid',
            path: 'grid',
            meta: { alias: '表格',isForm:false,moduleDesc:'地图集',dataDesc:'' },
            component: Grid
        },
        {
            name:'atlas-form',
            path: 'form',
            meta: { alias: '表单',isForm:true },
            component: Form
        }
    ]
}


