<template>
    <el-dialog title="附件地址添加" :visible="dialogFormVisible" class="fileAddress">
        <el-button @click="add">添加</el-button>
        <file-dialog :index="index" v-model="item.address" v-for="(item,index) in formData" :key="`${index}`" @del="del" :fileName="fileName" :url="url"></file-dialog>
        <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="save">确 定</el-button>
            <el-button @click="cancel">取 消</el-button>
        </div>
    </el-dialog>
</template>
<script>
import LAT from '@/models/business/LAT'
import LONG from '@/models/business/LONG'
import FileDialog from '#com/fileaddress-input'
export default {
    name:'file-Address',
    data() {
        return {
            formData: [],
            item: null,
            index: 0,
            fileName:'',  
            url:''          
        }
    },
    watch: {
        mapCoordinateData() {
            let arr = this.mapLocationStr2Array(this.fileaddressData);
            this.formData = arr;
        }
    },
    props: {
        dialogFormVisible: false,
        fileaddressData: null,
        currentName:''
    },
    methods: {
        mapLocationStr2Array(locStr) {
            if (!locStr) return [];            
        },
        add() {
            this.formData.push({});
        },
        del(index) {
            let temp = this.formData;
            temp.splice(index, 1);
            this.formData = temp;
        },
        save() {
            //组织成字符串
            let coordinateData = "";
            let urlPre=this.url;
            this.formData.forEach(function (item) {
                coordinateData +=urlPre+item.address+";";
            });
            this.$emit('save', coordinateData);
        },
        cancel() {
            this.$emit('cancel');
        },
        //获取当前时间
        getNowFormatDate() {
            var date = new Date();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            var currentdate = date.getFullYear() + month + strDate
            return currentdate;
        }
    },
    components: {
        FileDialog
    },
    created(){
        if(this.currentName!=''){
            this.fileName=this.currentName;
            this.url=`${this.fileName}/${this.getNowFormatDate()}/`;
        }
    }
}
</script>




