class PRIVS_USER {
    constructor(props) {
        props = props || {};
        this.NAME = props.NAME || "";
        this.REALNAME = props.REALNAME || "";
        this.PASSWORD = props.PASSWORD || "";
        this.ROLEID = props.ROLEID || "";
        this.IDCARD = props.IDCARD || "";
        this.SEX = props.SEX || "";
        this.USERCODE = props.USERCODE || "";
        this.MOBILE = props.MOBILE || "";
        this.QQ = props.QQ || "";
        this.EMAIL = props.EMAIL || "";
        this.CAREERENTRY = props.CAREERENTRY || "";
        this.LEAVEDATE = props.LEAVEDATE || "";
        this.TOKEN = props.TOKEN || "";
        this.ISSYSTEM = props.ISSYSTEM || "";
        this.ISLOCKED = props.ISLOCKED || "";
        this.REMARK = props.REMARK || "";
        this.BGWZ = props.BGWZ || "";
        this.BGDH = props.BGDH || "";
        this.CJRID = props.CJRID || "";
        this.GN = props.GN || "";
        this.SFYXGMM = props.SFYXGMM || "";
    }
}
export default PRIVS_USER;


