import User from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'user',
    path:'user',
    meta:{alias:'用户管理'},
    component:User,
    redirect:{name:'user-grid'},
    subRoutes:[
        {
            name:'user-grid',
            path:'grid',
            meta:{alias:'表格',isForm:false,moduleDesc:'用户管理',dataDesc:''},
            component:Grid
        },{
            name:'user-form',
            path:'form',
            meta:{alias:'表单',isForm:true},
            component:Form
        }
    ]
}


