<template>
    <div>
        <span>纬度：</span>
        <el-input class="formInput" v-model="item.lat.degree">
            <template slot="append">°</template>
        </el-input>
        <el-input class="formInput" v-model="item.lat.minutes">
            <template slot="append">′</template>
        </el-input>
        <el-input class="formInput" v-model="item.lat.seconds">
            <template slot="append">″</template>
        </el-input>
        <el-select class='formInput' placeholder="" v-model="item.lat.latitude">
            <el-option label="N" value="N"></el-option>
            <el-option label="S" value="S"></el-option>
        </el-select>
        <span style="margin-left:20px;">经度：</span>
        <el-input class="formInput" v-model="item.long.degree">
            <template slot="append">°</template>
        </el-input>
        <el-input class="formInput" v-model="item.long.minutes">
            <template slot="append">′</template>
        </el-input>
        <el-input class="formInput" v-model="item.long.seconds">
            <template slot="append">″</template>
        </el-input>
        <el-select class='formInput' placeholder="" v-model="item.long.longitude">
            <el-option class='formInput' label="E" value="E"></el-option>
            <el-option class='formInput' label="W" value="W"></el-option>
        </el-select>
        <i class="el-icon-minus" @click="del(index)" v-if="mapCoorFooterVisible"></i>
    </div>
</template>
<script>
export default {
    props:{
        item:null,
        index:0,
        mapCoorFooterVisible:Boolean
    },
    methods:{
        del(index) {
            this.$emit('del', index);
        }
    }
}
</script>



