<template>
    <div :class="style.gridPage">
        <div>
            <p :class="style.moduleInfo">
                <span>{{modoleDesc}}数据库管理</span>
                <span></span>
                <span :class="style.funcInfo">编辑、删除、新建{{dataDesc}}</span>
            </p>
        </div>
        <div :class="style.gridBorder" :style="{maxHeight:gridHeight+'px'}">
            <div :class="style.gridTitle">
                <span :class="style.totalLb">共{{total}}条{{dataDesc}}</span>
                <div :class="style.search">
                    <el-input size="small" placeholder="请输入关键字查询" @keyup.13="search" v-model="keyword" icon="close" :on-icon-click="handleIconClick" @change="handleKeywordChanged">
                    </el-input>
                </div>
                <div :class="style.searchbtn">
                    <el-button type="primary" size="small" @click="handleKeywordChanged">搜索</el-button>
                </div>
                <div :class="style.btns" v-if="hasAllPrivs">
                    <data-upload :uploaStyle="uploaStyle" :uploadURL="UPLOADURL" @uploadSuccessful="uploadSuccessful"> </data-upload>
                    <!-- <el-button type="primary" size="small" @click="loadData" v-if="notUserModule">导入</el-button> -->
                    <el-button type="primary" size="small" @click="exportData" v-if="notUserModule">导出</el-button>
                    <el-button type="primary" size="small" @click="addData">新建</el-button>
                    <el-button type="primary" size="small" :disabled="massDelBtndisabled" @click="massDelData">删除</el-button>
                    <!-- <el-button type="primary" size="small" :disabled="massDelBtndisabled" @click="publishData11">发布</el-button> -->
                </div>

            </div>
            <div :class="style.grid">
                <el-table :data="tableData" border stripe @selection-change="handleSelectionChange" :max-height="columnHeight">
                    <el-table-column type="selection" width="55" />
                    <el-table-column v-for="(item,index) in collums" :type="item.type" :key="index" :prop="item.prop" :label="item.label" :width="item.width" :formatter="mapMethod(item.formatter)">
                    </el-table-column>
                    <el-table-column label="操作" min-width="150">
                        <template scope="scope">
                            
                            <i class="readbtnIcon iconfont  icon-chakan" title="查看" @click="read(scope.$index, scope.row)"></i>
                            <div :class="style.btnIconsjg" v-if="!hasAllPrivs"/>
                            <div :class="style.btnIcons" v-if="hasAllPrivs">
                                <i class="btnIcon el-icon-edit" title="编辑" @click="edit(scope.$index, scope.row)"></i>
                                <i class="el-icon-delete btnIcon" title="删除" @click="del(scope.$index, scope.row)"></i>
                                <i class="el-icon-star-off btnIcon" title="设为特选" v-if="scope.row.SFSTXTM==='0'" @click="setToSelect(scope.$index, scope.row, true)"></i>
                                <i class="el-icon-star-on btnIcon" title="取消特选" v-if="scope.row.SFSTXTM==='1'" @click="setToSelect(scope.$index, scope.row, false)"></i>
                                <i class="fabubtnIcon iconfont icon-weifabu" title="发布" v-if="scope.row.SFFB==='0'" @click="publishData(scope.$index, scope.row, true)"></i>
                                <i class="fabubtnIcon iconfont icon-yifabu" title="取消发布" v-if="scope.row.SFFB==='1'" @click="publishData(scope.$index, scope.row, false)"></i>
                            </div>
                            <i class="downloadbtnIcon iconfont icon-xiazai" v-if="notUserModule"  title="下载" @click="download(scope.$index, scope.row)"></i>
                        </template>
                    </el-table-column>
                </el-table>
                <div :class="style.pageBtns">
                    <common-pagination :param="param" :total="total" @pageChange="handlePageChange"></common-pagination>
                </div>

            </div>
        </div>

    </div>
</template>
<script>
import CommonPagination from '#com/common-pagination'
import style from '@/css/common-grid.css'
import dataUploadStyle from '@/css/data-importing.css'
import QueryParam from '@/models/business/query-param'
import { mapGetters } from 'vuex'
import { user } from '@/components/modules/login/mutation-types'
import dataUpload from '#com/data-upload'
import {UPLOADURL} from '@/configs/upload-download'
export default {
    name: "common-grid",
    data() {
        return {
            style,
            keyword: "",
            gridSelctions: [],
            param: new QueryParam(),
            massDelBtndisabled: true,
            dataDesc: "",
            modoleDesc: "",
            selDatas: [],
            columnHeight: "700",
            gridHeight: 800,
            hasAllPrivs: false,
            notUserModule: true,
            uploaStyle:{buttonType:"primary", buttonsize:"small", buttonClass:dataUploadStyle.updatabtn,inputClass:dataUploadStyle.file,buttonname:"导入"},  
            UPLOADURL,
        };
    },
    props: {
        editable: false,
        collums: null,
        tableData: null,
        downloadVis:true,
        // dataDesc: "地图",
        // modoleDesc: "地图",
        // param: null,
        total: 0,
        // checkEditEnable: Function
    },
    components: {
        CommonPagination,
        dataUpload
    },
    methods: {
        handleIconClick() {
            this.keyword = "";
            this.handleKeywordChanged();
        },
        handleKeywordChanged() {
            this.param = new QueryParam();
            this.param.keyword = this.keyword;
            this.search();
        },
        handlePageChange(param) {
            this.param = param;
            this.search();
            // this.$emit('pageChange', param)
        },
        mapMethod(formatter) {
            return this[formatter];
        },
        fixedTo2(row, column, cellValue) {
            if (!cellValue || isNaN(cellValue)) return '';
            return cellValue.toFixed(2);
        },
        // transDateStr(row, column, cellValue) {
        //     if (!cellValue) return '';
        //     return this.transDateStr2normal(cellValue);
        // },
        read($index, row) {
            this.$emit('read', $index, row);
        },
        edit($index, row) {
            this.$emit('edit', $index, row);
        },
        del($index, row) {
            this.selDatas = [row.ID];
            this.toDel();
            // this.$emit('del', [row.ID]);
        },
        setToSelect($index, row, isTrue) {
            let lb = "";
            let value = "1";
            if (isTrue == false) {
                lb = "非";
                value = "0";
            }
            this.$confirm('确定设置为' + lb + '特选条目？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {

                this.$emit('toSpecial', [row.ID, value]);
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消设置'
                });
            });


        },
        download($index, row) {
            this.$emit('downLoad', row.ID);

        },
        handleSelectionChange(val) {
            this.gridSelctions = val;
            if (this.gridSelctions.length !== 0)
                this.massDelBtndisabled = false;
            else
                this.massDelBtndisabled = true;
            this.$emit('selected', val);
        },
        loadData() {

        },
        search() {
            this.$emit('search', this.param);
        },
        addData() {
            this.$emit('add');
        },
        exportData() {
            this.selDatas = this.gridSelctions.map(item => item.ID);
            this.$emit('exportData',this.selDatas,this.keyword);
        },
        massDelData() {
            this.selDatas = this.gridSelctions.map(item => item.ID);
            this.toDel();
        },
        toDel() {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$emit('del', this.selDatas);
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });

        },
        getDesc() {
            this.modoleDesc = this.$route.meta.moduleDesc;
            this.dataDesc = this.$route.meta.dataDesc;
        },
        //批量发布
        // publishData11() {
        //     this.selDatas = this.gridSelctions.map(item => item.ID);
        //     this.$emit('publish', this.selDatas, true);
        // },
        publishData($index, row, isTrue) {
            this.$emit('publish', [row.ID], isTrue);
        },
        judgePrivs() {
            if (this.user && this.user.hasOwnProperty('ROLEID') && this.user.ROLEID && this.user.ROLEID == "1")
                this.hasAllPrivs = true;
            else
                this.hasAllPrivs = false;
        },
        //導入附件上傳成功后回調
        uploadSuccessful(res){
             this.$emit('uploadSuccessful', res);
        },
    },
    watch: {
        // $route() {
        //     this.getDesc();
        // },
    },

    created() {
        this.getDesc();
        // this.judgePrivs();
        if (this.downloadVis == false)
                this.notUserModule = false;


    },
    mounted() {
        this.columnHeight = document.documentElement.clientHeight - 300;
        this.gridHeight = document.documentElement.clientHeight - 195;
        this.judgePrivs();

    },
    computed: {
        ...mapGetters({
            user
        }),

    }
    /* render 日后研究
    render(h) {
         let list = this.collums.map(item => {
             let defaultC = <TableColumn type={item.type}
                 prop={item.prop}
                 label={item.label}
                 width={item.width}
                 formatter={this.mapMethod(item.formatter)}></TableColumn>
             let read = <i class="el-icon-information" size="small" ></i>
             let edit = <i class="el-icon-edit" size="small" ></i>
             let del = <i class="el-icon-delete" size="small" ></i>
             if (item.template) {
                 <GridOperator opers={item.template.opers}></GridOperator>
                 if (item.template == 'operator' || item.template.name == 'operator') {
                     if (!item.template.opers) {
                         return <TableColumn>
                             type={item.type}
                             prop={item.prop}
                             label={item.label}
                             width={item.width}
                             formatter={this.mapMethod(item.formatter)}>
                             <template scope="scope">
                                 {read}
                                 {edit}
                                 {del}
                             </template>
                         </TableColumn>
                     } else if (item.template.opers.length) {
                         let list = [];
                         item.template.opers.forEach(oper => {
                             if (oper == 'read') {
                                 list.push(read);
                             }
                             if (oper == 'edit') {
                                 list.push(edit);
                             }
                             if (oper == 'delete') {
                                 list.push(del);
                             }
                         })
                         console.log(list);
                         return <TableColumn>
                             <template>
                                 {list}
                             </template>
                         </TableColumn>
                     } else {
                         return defaultC
                     }
                     
                 } else {
                     return defaultC
                 }
             } else {
                 return defaultC
             }
         })
         return <div>
             <Table data={this.tableData}>
                 {list}
             </Table>
             <CommonPagination param={this.param}
                 total={this.total}
                 onPageChange={this.handlePageChange}>
             </CommonPagination>
         </div>
     }
    */
}
</script>


