import { sysClient } from '@/clients/hprose-client'
import * as Types from './mutation-types'
import QueryParam from '@/models/business/query-param'

const state = {
    queryParam: new QueryParam()
}

const getters = {
    [Types.queryParam]: ({ queryParam }) => queryParam
}

const mutations = {
    [Types.SET_QUERYPARAM]: (state, queryParam) => state.queryParam = queryParam
}
const actions = {
    [Types.getRecords]: (context, args) => sysClient.getData('getUserList', args, false),
    [Types.getUserInfo]: (context, args) => sysClient.getData('getUserInfo', args, false),
    [Types.delData]: (context, args) => {
        //删除或批量删除
        return sysClient.getData('dropUserInfo', args, true);
    },

    [Types.addUserInfo]: (context, args) => sysClient.getData('addUserInfo', args, true),
    [Types.usernameVeri]: (context, args) => sysClient.getData('usernameVeri', args, true),

    [Types.editUserInfo]: (context, args) => sysClient.getData('editUserInfo', args, true),
    [Types.passwordVeri]: (context, args) => sysClient.getData('passwordVeri', args, true),
}
export default {
    state,
    getters,
    mutations,
    actions
}


