import { sysClient } from '@/clients/hprose-client'
import * as Types from './mutation-types'
import QueryParam from '@/models/business/query-param'

const state = {
    queryParam: new QueryParam()
}

const getters = {
    [Types.queryParam]: ({ queryParam }) => queryParam
}

const mutations = {
    [Types.SET_QUERYPARAM]: (state, queryParam) => state.queryParam = queryParam
}

const actions = {
    [Types.getRecords]: (context, args) => {
        return sysClient.getData('getImageDataList', args);
    },
    [Types.delData]: (context, args) => sysClient.getData('deleteImageData', args, true),

    [Types.getFormData]: (context, args) => {
        return sysClient.getData('seeImageData', args);
    },
    [Types.addRecords]: (context, args) => {
      return sysClient.getData('putImageData', args, true);
    },
    [Types.editRecords]: (context, args) => sysClient.getData('edittImageData', args, true),
    [Types.signMapData]: (context, args) =>
    {
        // console.log(args);
       return sysClient.getData('signImageData', args, true);
    }, 


    [Types.getCompanyData]: (context, args) => sysClient.getData('getCompanyList', args, false),
    [Types.getLanguageList]: (context, args) => sysClient.getData('getLanguage', args, false),
    [Types.getPeriodList]: (context, args) => sysClient.getData('getPeriod', args, false),
    [Types.getRegionList]:(context,args)=>sysClient.getData('getRegion', args, false),
    [Types.publishData]:(context,args)=>sysClient.getData('PublishImageData',args,true),
    [Types.getEnclPath]:(context,args)=>sysClient.getData('getImagePath',args,false),
    [Types.exportData]:(context,args)=>sysClient.getData('ExportImageData',args,true),
    [Types.importData]:(context,args)=>sysClient.getData('ImportImageleData',args,true),
}
export default {
    state,
    getters,
    mutations,
    actions
}





