<template>
    <div :class="style.basicinf">
        <div :class="style.form">
            <div :class="style.basicinfLeft">
                <span>基本信息</span>
            </div>
            <div :class="style.basicinfRight">
                <el-form ref="formData" :model="formData" :rules="rules">
                    <div :class="style.list_row">
                        <div :class="style.list_cell">
                            <div>
                                <div :class="style.field">名称</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SPMC">
                                        <el-input :readonly="state==EditState.READ" class="vali" v-model="formData.SPMC"></el-input>
                                    </el-form-item>
                                </div>
                            </div>

                            <div>
                                <div :class="style.field">作者</div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" readonly v-model="formData.ZZ" @focus="editAuthor"></el-input>
                                    <i class="el-icon-edit" @click="editAuthor"></i>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">电子文件名称</div>
                                <div :class="style.fieldValue">
                                    <el-input readonly class="content" v-model="formData.DZWJMC" @focus="editDZWJMC"></el-input>
                                    <i class="el-icon-edit" @click="editDZWJMC"></i>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">生成时间</div>
                                <div :class="style.fieldValue">
                                    <el-date-picker class="dateEditor" :readonly="state==EditState.READ" type="date" placeholder="选择日期" v-model="formData.SCSJ"></el-date-picker>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">提供单位</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="TGDWMC">
                                        <!-- <el-select class="content" v-model="formData.TGDWID" placeholder="请选择提供单位" /> -->
                                        <el-autocomplete class="inline-input-vali" :disabled="state==EditState.READ" v-model="state2" :fetchSuggestions="querySearch" placeholder="请输入内容" @select="handleSelect" :trigger-on-focus="false">
                                        </el-autocomplete>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">提供单位坐标</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="TGDWZB">
                                        <el-input class="vali" :readonly="state==EditState.READ" v-model="formData.TGDWZB" @focus="editCoordinate"></el-input>
                                        <i class="el-icon-edit" @click="editCoordinate"></i>
                                        <i class="pointPickup" @click="toPickup"></i>
                                    </el-form-item>
                                </div>
                            </div>

                        </div>
                        <div :class="style.list_cell">
                            <div>
                                <div :class="style.field">语言</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="YYID">
                                        <el-select multiple  class="vali" v-model="formData.YYID" :disabled="state==EditState.READ">
                                            <el-option v-for="(item,index) in languageList" :key="`${index}`" :value="item.ID" :label="item.MC"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">格式</div>
                                <div :class="style.fieldValue">
                                    <el-input class="content" :readonly="state==EditState.READ" v-model="formData.GS"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">时长</div>
                                <div :class="style.fieldValue">
                                    <el-input class="content" :readonly="state==EditState.READ" v-model="formData.SC"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">关键词</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="GJC">
                                        <el-input readonly class="vali" placeholder="多个关键词请用逗号分隔" v-model="formData.GJC" @focus="editKeyword"></el-input>
                                        <i class="el-icon-edit" @click="editKeyword"></i>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">受控度</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SKD">
                                        <el-select class="vali" v-model="formData.SKD" :disabled="state==EditState.READ">
                                            <el-option v-for="(item,index) in skdList" :key="`${index}`" :value="item.id" :label="item.name"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>

                            <div>
                                <div :class="style.field">时期范围</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SQFWID">
                                        <el-select :disabled="state==EditState.READ" class="vali" v-model="formData.SQFWID">
                                            <el-option v-for="(item,index) in periodList" :key="`${index}`" :value="item.ID" :label="item.SQFW"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>

                        </div>
                        <div :class="style.list_cell">

                            <div>
                                <div :class="style.field">来源</div>
                                <div :class="style.fieldValue">
                                    <el-input class="content" :readonly="state==EditState.READ" v-model="formData.LY"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">网址</div>
                                <div :class="style.fieldValue">
                                    <el-input class="content" :readonly="state==EditState.READ" v-model="formData.WZ"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">附件地址</div>
                                <div :class="style.fieldValue">
                                    <el-input v-if="isVisable" class="content" placeholder="请输入内容" v-model="formData.LJ" :readonly="state==EditState.READ">
                                        <template slot="prepend">{{url}}</template>
                                    </el-input>
                                    <el-input v-else class="content" readonly v-model="formData.LJ">
                                    </el-input>
                                    <!-- <i class="el-icon-edit" @click="addAddress"></i> -->
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div :class="style.basicinfRightbottom">
                        <div>
                            <div :class="style.fieldBottom">描述</div>
                            <div :class="style.fieldValueBottom">
                                <el-input class="fieldValueBottom" :readonly="state==EditState.READ" type="textarea" v-model="formData.MS" placeholder="说明该资料的时间、地点、作者、位置、时间、原因；该图中所发现信息的重点提示。 比如：该图中：钓鱼岛、赤尾屿用数字代号加脚注形式表示 "></el-input>
                            </div>
                        </div>
                        <div>
                            <div :class="style.fieldBottom">备注</div>
                            <div :class="style.fieldValueBottom">
                                <el-input class="fieldValueBottom" :readonly="state==EditState.READ" type="textarea" placeholder="关于资料需要额外说明信息" v-model="formData.BZ"></el-input>
                            </div>
                        </div>
                        <div>
                            <div :class="style.pic">
                                <span>缩略图</span>
                            </div>
                            <div :class="style.picBottom">
                                <div></div>
                                <div>
                                    <!-- <el-button :class="style.picbtn" type="success">上传缩略图</el-button> -->
                                    <el-upload class="avatar-uploader" v-if="showSLT" :disabled="state==EditState.READ" :action="UPLOADURL" :show-file-list="false" :on-success="handleAvatarSuccess">
                                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-form>
            </div>
        </div>
        <div :class="style.btndiv" id="twoBtn" v-if="state!==EditState.READ">
            <el-button class="sureBtn" @click="validate('formData')">
                确定
            </el-button>
            <el-button class="cancelBtn" @click="cancel('formData')">
                取消
            </el-button>
        </div>
        <company-Coordinate :mapCoordinateData="mapCoordinateData" :dialogFormVisible="dialogFormVisible" @save="savebtn" @cancel="cancelbtn" :mapCoorFooterVisible="mapCoorFooterVisible"></company-Coordinate>
        <author-msg :authorData="authorData" :authorDialogVisible="authorDialogVisible" @save="saveAuthor" @cancel="cancelAuthor" :autherFooterVisible="autherFooterVisible"></author-msg>
        <keyword-dialog :keywordDialogVisible="keywordDialogVisible" :keywordData="keywordData" @save="saveKeyword" @cancel="cancelKeyword" :keywordFooterVisible="keywordFooterVisible"></keyword-dialog>
        <electronfile-dialog :electronfiledDialogVisible="electronfiledDialogVisible" :electronFileData="electronFileData" :electronFileFooterVisible="electronFileFooterVisible" @save="saveElectronfile" @cancel="cancelElectronfile"></electronfile-dialog>
        <point-pickup v-if="pickupFormVisible" :point="point" :allowPickup="allowPickup" @cancel="cancelbtn" @save="savebtn"></point-pickup>
        <!-- <file-address :fileaddressData="fileaddressData" :dialogFormVisible="fileDialogVis" :currentName="currentName" @save="saveAddress" @cancel="cancelAddress"></file-address> -->
    </div>
</template>

<script>
import { DOWNLOADURL, UPLOADURL } from '@/configs/upload-download'
import style from '@/css/video-form.css'
import * as  EditState from '@/enums/edit-state'
import OM_VEDIO from '@/models/entity/OM_VEDIO'
import { addRecords, getCompanyData, getLanguageList, getPeriodList, editRecords, readRecord } from './mutation-types'
import { mapActions, mapMutations } from 'vuex'
import companyCoordinate from '#com/company-coordinate'
import FileAddress from '#com/file-address'
import AuthorMsg from '#com/author-msg'
import { rules } from '@/util/rules.js'
import KeywordDialog from '#com/keyword-dialog'
import electronfileDialog from '#com/electronfile-dialog'
import PointPickup from '#com/point-map-pickup'
export default {
    name: 'video-form',
    data() {
        return {
            style,
            state: null,
            EditState,
            formData: new OM_VEDIO(),
            tmList: [{ id: '0', name: '否' }, { id: '1', name: '是' }],
            skdList: [{ id: '0', name: '公开' }, { id: '1', name: '受控' }],
            companyListData: [],
            state2: '',
            languageList: [],
            periodList: [],
            mapCoordinateData: "",
            dialogFormVisible: false,
            mapCoorFooterVisible:true,
            //附件地址相关
            fileaddressData: null,
            fileDialogVis: false,
            currentName: 'vedio',
            url: '',
            //作者组件
            authorData: "",
            authorDialogVisible: false,
            autherFooterVisible: true,
            rules: rules,
            isVisable: true,
            DOWNLOADURL,
            UPLOADURL,
            imageUrl: '',
            imgPath: '',
            showSLT: true,
            //关键词组件可见性及数据源
            keywordDialogVisible: false,
            keywordData: "",
            keywordFooterVisible: true,
             //电子文件名称组件可见性及数据源等
            electronfiledDialogVisible: false,
            electronFileData: "",
            electronFileFooterVisible: true,
            //坐标拾取相关
            pickupFormVisible: false,
            point: null,
            allowPickup: true,
        }
    },
    methods: {
        querySearch(queryString, cb) {
            var companyListData = this.companyListData;
            var results = queryString ? companyListData.filter(this.createFilter(queryString)) : companyListData;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
            return (companyListData) => {
                if (companyListData.value) {
                    return (companyListData.value.indexOf(queryString.toLowerCase())!=-1);
                }
            };
        },
        validate(formName) {
            let val;
            this.$refs[formName].validate((valid) => {
                val = valid;
                if (valid) {
                    this.savedata();
                } else {
                    return false;
                }
            });
            if (val) return;
            this.$notify({
                title: '提示',
                message: "请填写必填项",
                duration: 3000,
                type: 'info',
                offset: 125,
            });
        },
        async savedata() {
            // let result = null;
            let form = new OM_VEDIO(this.formData);
            if (!form) return;
            //TGDWZB转成十进制，经度在前纬度在后
            if (form.TGDWZB) {
                let arr = form.TGDWZB.split(",");
                let lon = arr[1].slice(arr[1].length - 1, arr[1].length);
                let lat = arr[0].slice(arr[0].length - 1, arr[0].length)
                form.TGDWZB = this.covertDegree(arr[1], lon) + ',' + this.covertDegree(arr[0], lat);
            }
            if (!this.state2) {
                form.TGDWMC = "未知单位";
                form.TGDWID = 1;
                form.TGDWZB = "";
            }
            if (form.SCSJ) {
                form.SCSJ = this.transDate2DatetimeStr(form.SCSJ).split(' ')[0];
            }
            if (form.GJC && form.GJC.indexOf('，')) {
                form.GJC = form.GJC.replace('，', ',');
            }
            if (form.LJ != ""&&this.isVisable) {
                form.LJ = this.url + form.LJ;
            }
            if (form.YYID) {
                form.YYID = this.createyyFilter(form.YYID);
            }
            form.SLT = this.imgPath;
            let formData = JSON.stringify(form);
            if (this.state == EditState.ADD) {
                let result = await this.addRecords(formData);             
                if (result.status) {
                    this.showSuccess();
                    this.$router.push({ name: 'video-grid' });
                } else {
                    this.showError();
                }
            }
            if (this.state == EditState.EDIT) {
                let result = await this.editRecords(formData);
                if (result.status) {
                    this.showSuccess();
                    this.$router.push({ name: 'video-grid' });
                } else {
                    this.showError();
                }
            }
        },
        ...mapActions({
            addRecords,
            getCompanyData,
            getLanguageList,
            getPeriodList,
            editRecords,
            readRecord
        }),
        handleSelect(item) {
            // this.formData.TGDWID = item.id;
            // this.formData.TGDWMC = item.value;
            // if (item.loc) {
            //     let arr = item.loc.split(',');
            //     this.formData.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
            // }
            this.focusout();
        },
        focusout() {
            if (!this.state2) { this.formData.TGDWZB = ""; return; }
            let currenttgdw = this.companyListData.find(item => item.value == this.state2);
            if (!currenttgdw) {
                this.formData.TGDWMC = this.state2;
                this.formData.TGDWID = 0;
            }
            else {
                this.formData.TGDWMC = this.state2;
                this.formData.TGDWID = currenttgdw.id;
                if (currenttgdw.loc) {
                    let arr = currenttgdw.loc.split(',');
                    this.formData.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
                }
            }
        },
        async getcompanyList() {
            let companyData = await this.getCompanyData();
            if (!companyData) return;
            this.companyListData = companyData.map(item => {
                return { value: item.MC, id: item.ID, loc: item.TGDWZB };
            });
            // this.companyListData = companyData;
        },
        //获取语言列表
        async getlanguageData() {
            let languageData = await this.getLanguageList();
            if (!languageData) return;
            this.languageList = languageData;
        },
        createyyFilter(queryArray) {
            let filterlgArr = [];
            for (let i = 0; i < queryArray.length; i++) {
                let id = queryArray[i];
                for (let m = 0; m < this.languageList.length; m++) {
                    if (this.languageList[m].ID == id) {
                        filterlgArr.push(this.languageList[m]);
                    }

                }
            }
            return filterlgArr;
        },

        //获取时期范围
        async getPeriodData() {
            let periodData = await this.getPeriodList();
            if (!periodData) return;
            this.periodList = periodData;
        },
        //成功提示
        showSuccess() {
            this.$message({
                message: '保存成功',
                type: 'success'
            });
        },
        //保存不成功提示
        showError() {
            this.$message({
                message: '保存失败',
                type: 'warning'
            });
        },
        //取消
        cancel(formName) {
            this.formData = new OM_VEDIO();
            this.$refs[formName].resetFields();
        },
        editCoordinate() {
            if (this.state == EditState.READ) this.mapCoorFooterVisible = false;
            else this.mapCoorFooterVisible = true;
            this.dialogFormVisible = true;
            this.mapCoordinateData = this.formData.TGDWZB;
        },
        //编辑坐标工具栏的确定和取消
        cancelbtn() {
            this.dialogFormVisible = false;
            this.pickupFormVisible = false;
        },
        savebtn(param) {
            this.dialogFormVisible = false;
            this.formData.TGDWZB = param;
            this.pickupFormVisible = false;
        },
        //附件地址添加、保存和取消
        addAddress() {
            this.fileDialogVis = true;
            this.fileaddressData = this.formData.LJ;
        },
        saveAddress(param) {
            this.fileDialogVis = false;
            this.formData.LJ = param;
        },
        cancelAddress() {
            this.fileDialogVis = false;
        },
        //查看单条数据详情
        async readData(id) {            
            let result = await this.readRecord(id);
            if (!result) return;           
            if (result.hasOwnProperty('TGDWMC') && result.hasOwnProperty('TGDWZB')) {
                this.state2 = result.TGDWMC;
                this.point = result.TGDWZB;
                //需要转一下获取的TGDWZB，将十进制转成度分秒
                if (result.TGDWZB) {
                    let arr = result.TGDWZB.split(',');
                    result.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
                }
            }
            if (!result.LJ) {
                this.isVisable = true;
            } else {
                this.isVisable = false;
            }
            if (result.YYID)
                result.YYID = result.YYID.map(item => item.ID);
            if (result.SLT)
                this.imageUrl = this.DOWNLOADURL + result.SLT;
            if (result.SLT == "" && this.state == EditState.READ)
                this.showSLT = false;
            this.formData = result;
        },
        //dialog编辑完之后点击确定
        saveAuthor(param) {
            this.authorDialogVisible = false;
            this.formData.ZZ = param;
        },
        cancelAuthor() {
            this.authorDialogVisible = false;
        },
        //编辑作者
        editAuthor() {
            if (this.state == EditState.READ) this.autherFooterVisible = false;
            else this.autherFooterVisible = true;
            this.authorDialogVisible = true;
            this.authorData = this.formData.ZZ;
        },
        //获取当前时间
        getNowFormatDate() {
            var date = new Date();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            var currentdate = date.getFullYear() + month + strDate
            return currentdate;
        },
        //编辑关键词
        editKeyword() {
            if (this.state == EditState.READ)
                this.keywordFooterVisible = false;
            else
                this.keywordFooterVisible = true;
            this.keywordDialogVisible = true;
            this.keywordData = this.formData.GJC;
        },
        //保存关键词
        saveKeyword(gjcdata) {
            this.keywordDialogVisible = false;
            if (!gjcdata) return;
            this.formData.GJC = gjcdata;

        },
        //关闭关键词dialog
        cancelKeyword() {
            this.keywordDialogVisible = false;
        },
         //编辑电子文件名称
        editDZWJMC() {
            if (this.state == EditState.READ) this.electronFileFooterVisible = false;
            else this.electronFileFooterVisible = true;
            this.electronfiledDialogVisible = true;
            this.electronFileData = this.formData.DZWJMC;
        },
        //保存电子文件名称
        saveElectronfile(dzwjmcdata) {
            if (!dzwjmcdata) return;
            this.electronfiledDialogVisible = false;
            this.formData.DZWJMC = dzwjmcdata;
        },
        //关闭电子文件名称dialog
        cancelElectronfile() {
            this.electronfiledDialogVisible = false;
        },
        //定义外层的高度
        getHoleHeight() {
            let div = document.getElementById('basic');
            let holeHeight = document.body.clientHeight - 140;
            div.style.height = holeHeight + 'px';
        },
        //查看时，确定和取消按钮不显示
        sureOrCancelVis() {
            let btndiv = document.getElementById('twoBtn');
            btndiv.style.display = "none";
        },
        // 上传缩略图
        handleAvatarSuccess(res, file) {
            this.imgPath = res;
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        //坐标拾取
        toPickup() {
            if (this.state == EditState.READ)
                this.allowPickup = false;
            this.pickupFormVisible = true;

        },
    },
    mounted() {
        // this.companyListData = this.loadAll();
        this.getcompanyList();
        this.getlanguageData();
        this.getPeriodData();

        this.$el.getElementsByClassName('inline-input-vali')[0].querySelector("input").onblur = this.focusout;//获取提供单位名称组件的鼠标移开事件
        // if (this.state == "read") {
        //     this.sureOrCancelVis();
        // }
    },
    created() {
        let param = this.$route.query;
        if (!param) return;
        if (param.state != null)
            this.state = param.state;
        this.mapCoordinateData = "";
        if (param.id != null) {
            if (this.state == "read" || this.state == "edit") this.readData(param.id);
        }
        this.url = `${this.currentName}/${this.getNowFormatDate()}/`;
    },
    components: {
        companyCoordinate,
        FileAddress,
        AuthorMsg,
        KeywordDialog,
        electronfileDialog,
        PointPickup
    },
}
</script>




