import Video from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'video',
    path:'video',
    meta:{alias:'专著'},
    component:Video,
    redirect:{name:'video-grid'},
    subRoutes:[
        {
            name:'video-grid',
            path:'grid',
            meta:{alias:'表格',isForm:false,moduleDesc:'专著',dataDesc:''},
            component:Grid
        },{
            name:'video-form',
            path:'form',
            meta:{alias:'表单',isForm:true},
            component:Form
        }
    ]
}


