<template>
  <div>
    <div>
      <el-button :type="uploaStyle.buttonType" :size="uploaStyle.buttonsize" :class="uploaStyle.buttonClass">{{uploaStyle.buttonname}}</el-button>
      <input type="file" name="file" id="fileinput" :class="uploaStyle.inputClass" @change="filechange">
    </div>
    <el-dialog title="上传进度" :visible.sync="dialogVisible" size="tiny" :before-close="handleClose" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="true">
      <div>
        <el-progress :text-inside="true" :stroke-width="18" :percentage="percentage"></el-progress>
        <p :class="style.label">正在上传，请稍候...</p>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import style from '@/css/data-importing.css'
export default {
  name: 'excel-data-import',
  props: {
    postData: null,
    uploadURL: null,
    maxfileSize: {
      type: Number,
      default: 50
    },
    uploaStyle:{
      type:Object,
       default:function() {
               return {
              buttonType:"primary",
             buttonsize:"small",
             buttonClass:null,
             inputClass:style.file,
             buttonname:"上传"
   };}
      
    }
    
  },
  data() {
    return {
      message: "",
      file: null,
      percentage: 0,
      style,
      dialogVisible: false,
      request: new XMLHttpRequest()
    }
  },
  methods: {
    handleClose(done) {
      this.$confirm("关闭将终止上传，确认关闭？")
        .then(_ => {
          this.request.abort();
          done();
        })
        .catch(_ => { });
    },
    filechange() {
      let filesize = this.file.files[0].size / 1024 / 1024;
      if (filesize > this.maxfileSize) {
        alert("附件不能大于" + this.maxfileSize + "M");
        fileinput.value = null;
        return
      }
      this.dialogVisible = true;
      this.submitFile();
    },
    submitFile() {
      let formdata = new FormData();
      if (!formdata || !this.request) return;
      if (this.postData && Object.keys(this.postData).length) {
        Object.keys(this.postData).forEach(key => formdata.append(key, this.postData[key]));
      }
      formdata.append('file', this.file.files[0]);
      this.request.open("POST", this.uploadURL, true);
      this.request.onload = (oEvent) => {
        this.message = this.request.status == 200 ? "上传成功!" : `Error ${this.request.status} occurred when trying to upload your file.`;   
        this.$emit('uploadSuccessful', this.request.response);//上传成功派发事件用于刷新界面等
        this.dialogVisible = false;
        fileinput.value = null;
      };
      this.request.onabort = (oEvent) => {fileinput.value = null; };
      this.request.upload.onprogress = (e) => {
        if (e.lengthComputable)
          this.percentage = Math.floor((e.loaded / e.total) * 100);
      };
       this.request.timeout = 0;
      //上传时间限制
      // var ajaxAbortTimer = setTimeout(function () {
      //   if (request) {
      //     request.abort();
      //     request = null;
      //   }
      // }, 5000);
      this.request.send(formdata);
    }
  },
  mounted() {
    this.file = this.$el.getElementsByTagName("input")[0];
  }
}
</script>


