<template>
    <div>

        <div :class="style.line1">
            <hr :class="style.lineyellow" />
        </div>
        <save-button v-if="state!=EditState.READ" :class="style.saveButton" :saveData="validData"></save-button>
        <div :class="style.basic">

            <div>
                <el-tag class="stable-form">基本信息</el-tag>
                <hr :class="style.lineblue" />
            </div>
            <el-form ref="formData" :model="formData" :class="style.list" :rules="rules" label-width="160px" :label-position="labelPosition">
                <el-form-item label="用户名：" prop="NAME">
                    <el-input v-model="formData.NAME" :readonly="state!=EditState.ADD" class="stable-form" size="small" @blur="valiUserName"></el-input>
                </el-form-item>
                <el-form-item label="真实姓名：" prop="REALNAME">
                    <el-input v-model="formData.REALNAME" :readonly="state!=EditState.ADD" class="stable-form" size="small"></el-input>
                </el-form-item>
                <el-form-item label="角色：" prop="ROLEID">
                    <el-select class="stable-form" :disabled="state!=EditState.ADD" v-model="formData.ROLEID">
                        <el-option v-for="(item,index) in roleList" :key="`${index}`" :value="item.id" :label="item.name"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="原密码：" v-if="state==EditState.EDIT" prop="OLDPASSWORD">
                    <el-input v-model="OLDPASSWORD" type="password" :readonly="state!=EditState.EDIT" class="stable-form" size="small" @blur="valiPassWord"></el-input>
                </el-form-item>
                <el-form-item label="新密码：" v-if="state==EditState.EDIT" prop="NEWPASSWORD">
                    <el-input v-model="NEWPASSWORD" type="password" :readonly="state!=EditState.EDIT" class="stable-form" size="small"></el-input>
                </el-form-item>
                <el-form-item label="密码：" v-if="state==EditState.ADD" prop="PASSWORD">
                    <el-input v-model="formData.PASSWORD" type="password" :readonly="state!=EditState.ADD" class="stable-form" size="small"></el-input>
                </el-form-item>
                <el-form-item label="确认密码：" v-if="state!=EditState.READ">
                    <el-input v-model="ENPASSWORD" type="password" class="stable-form" size="small"></el-input>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import style from '@/css/user-form.css'
import { getUserInfo } from './mutation-types'
import { addUserInfo, usernameVeri } from './mutation-types'
import { editUserInfo, passwordVeri } from './mutation-types'
import { mapActions } from 'vuex'
import * as  EditState from '@/enums/edit-state'
import PRIVS_USER from '@/models/entity/PRIVS_USER'
import SaveButton from '#com/save-button'
import { rules } from '@/util/rules.js'
export default {
    name: "user-form",
    data() {
        return {
            style,
            formData: new PRIVS_USER(),
            labelPosition: 'left',
            formId: '',
            state: '',
            EditState,
            roleList: [{ id: '1', name: '超级用户' }, { id: '2', name: '普通用户' }],
            OLDPASSWORD: '',
            NEWPASSWORD: '',
            ENPASSWORD: '',
            rules: rules,
            flag: ''
        }
    },
    components: {
        SaveButton
    },
    created() {
        let { id, state } = this.$route.query;
        this.formId = id;
        this.state = state;
        if (this.state != EditState.ADD) this.getFormData();
    },
    methods: {
        async getFormData() {
            let result = await this.getUserInfo(this.formId);
            if (!result) return;
            this.formData = result;
        },
        ...mapActions({
            getUserInfo,
            addUserInfo,
            usernameVeri,
            editUserInfo,
            passwordVeri
        }),
        validData() {
            if (this.state == EditState.ADD) {
                if (!this.ENPASSWORD) {
                    this.$message({
                        message: '请输入确认密码！',
                        type: 'warning'
                    });
                    return;
                }
                this.$refs['formData'].validate((valid) => {
                    if (valid) {
                        if (this.flag == "success") {
                            if (this.ENPASSWORD != this.formData.PASSWORD) {
                                this.$message({
                                    message: '两次密码输入不一致！',
                                    type: 'warning'
                                });
                            } else {
                                this.addUser();
                            }
                        }
                        else {
                            this.$message({
                                message: '用户名已存在！',
                                type: 'warning'
                            });
                        }
                    } else {
                        this.$message({
                            message: '请填写必填项！',
                            type: 'warning'
                        });
                        return false;
                    }
                });
            }
            if (this.state == EditState.EDIT) {
                if (!this.NEWPASSWORD || !this.OLDPASSWORD) {
                    this.$message({
                        message: '请填写密码！',
                        type: 'warning'
                    });
                    return;
                }
                if (!this.ENPASSWORD) {
                    this.$message({
                        message: '请输入确认密码！',
                        type: 'warning'
                    });
                    return;
                }
                this.formData.PASSWORD = this.NEWPASSWORD;
                if (this.flag == "success") {
                    if (this.ENPASSWORD != this.NEWPASSWORD) {
                        this.$message({
                            message: '两次密码输入不一致！',
                            type: 'warning'
                        });
                    } else {
                        this.editUser();
                    }
                }
                else {
                    this.$message({
                        message: '原密码不正确！',
                        type: 'warning'
                    });
                }
            }
        },
        async addUser() {
            let result = await this.addUserInfo(JSON.stringify(this.formData));
            if (!result) return;
            if (result.msg == 'fail') {
                this.$message({
                    message: result.result,
                    type: 'warning'
                });
            }
            if (result.msg == 'success') {
                this.$message({
                    message: result.result,
                    type: 'info'
                });
                this.$router.go(-1);
            }
        },
        async editUser() {
            let obj = new PRIVS_USER(this.formData);
            obj.ID = this.formId;
            let result = await this.editUserInfo(JSON.stringify(obj));
            if (!result) return;
            if (result.msg == 'fail') {
                this.$message({
                    message: result.result,
                    type: 'warning'
                });
            }
            if (result.msg == 'success') {
                this.$message({
                    message: result.result,
                    type: 'info'
                });
                this.$router.go(-1);
            }
        },
        async valiUserName() {
            if (!this.formData.NAME || this.state != EditState.ADD) return;
            let result = await this.usernameVeri(this.formData.NAME);
            if (!result) return;
            this.flag = result.msg;
            if (result.msg == 'fail') {
                this.$message({
                    message: result.result,
                    type: 'warning'
                });
            }
            if (result.msg == 'success') {
                this.$message({
                    message: result.result,
                    type: 'info'
                });
            }
        },
        async valiPassWord() {
            if (!this.OLDPASSWORD) return;
            let result = await this.passwordVeri([this.formId, this.OLDPASSWORD]);
            this.flag = result.msg;
            if (result.msg == 'fail') {
                this.$message({
                    message: result.result,
                    type: 'warning'
                });
            }
            if (result.msg == 'success') {
                this.$message({
                    message: result.result,
                    type: 'info'
                });
            }
            if (!result) return;
        }
    }

}
</script>



