import { sysClient } from '@/clients/hprose-client'
import * as Types from './mutation-types'
import QueryParam from '@/models/business/query-param'

const state = {
    queryParam: new QueryParam()
}

const getters = {
    [Types.queryParam]: ({ queryParam }) => queryParam
}

const mutations = {
    [Types.SET_QUERYPARAM]: (state, queryParam) => state.queryParam = queryParam
}

const actions = {
    [Types.getCompanyData]: (context, args) => sysClient.getData('getCompanyList', args, false),
    [Types.getLanguageList]: (context, args) => sysClient.getData('getLanguage', args, false),
    [Types.getPeriodList]: (context, args) => sysClient.getData('getPeriod', args, false),
    [Types.addRecords]: (context, args) => sysClient.getData('addMonograph', args, true),
    [Types.readRecord]:(context,args) => sysClient.getData('getMonographDetail', args, true),
    [Types.setMonSpeList]:(context,args) => sysClient.getData('setMonSpeList', args, true),
    [Types.editRecords]: (context, args) => {
        return sysClient.getData('editMonographInfo', args, true)
    },
    [Types.getRecords]: (context, args) => {
        return sysClient.getData('getMonographList', args);
    },
    [Types.delData]: (context, args) => {
        //删除或批量删除
        return sysClient.getData('dropMonographData', args, true);
    },
    [Types.publishData]:(context,args) => sysClient.getData('PublishMonographData', args, true),
    [Types.getEnclPath]:(context,args)=>sysClient.getData('getEnclfilePath',args,false),
    [Types.exportData]:(context,args)=>sysClient.getData('monographExport',args,true),
    [Types.importData]:(context,args)=>sysClient.getData('videoImport',args,true),
}
export default {
    state,
    getters,
    mutations,
    actions
}





