<template>
    <el-dialog title="地图关注点坐标信息" :visible="true" :before-close="cancel" @open="initMap">
        <div id="map-content" :class="style.layout">

        </div>
        <span slot="footer" class="dialog-footer" v-if="allowPickup">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="save">确 定</el-button>
        </span>
    </el-dialog>
</template>
<script>
import style from '@/css/point-map-pickup-multi.css'
export default {
    name: 'point-pickup-multi',

    data() {
        return {
            style,
            location: null,
            map: null,
            marker: null,
            markers: [],
            formatMarkers: '',
            isDouble: false
        }
    },
    props: {
        points: null,
        allowPickup: true,
    },
    methods: {
        //地图相关
        initMap() {
            let baseVecLayer = new L.TileLayer.WMTS("http://t0.tianditu.com/vec_c/wmts", {
                tileSize: 256,
                layer: 'vec',
                style: "default",
                tilematrixSet: "c",
                format: "tile",
            });
            let baseCvaLayer = new L.TileLayer.WMTS("http://t0.tianditu.com/cva_c/wmts", {
                tileSize: 256,
                layer: 'cva',
                style: "default",
                tilematrixSet: "c",
                format: "tile",

            });
            let latlng = L.latLng(40, 120);
            this.map = L.map('map-content', { center: latlng, zoom: 3, doubleClickZoom: false, crs: L.CRS.EPSG4326, layers: [baseVecLayer, baseCvaLayer] });
            if (this.allowPickup == true) {
                this.map.on("click", this.mapClick);
            }
            if (this.points != null) {
                this.transDatas(this.points);
            }
        },
        transDatas(datas) {
            this.formatMarkers = datas;
            let arr = datas.split(";");
            if (arr.length != 0) {
                for (let i = 0; i < arr.length; i++) {
                    let locat = arr[i].split(",");
                    console.log(10000, locat);
                    if (locat != '') {
                        let lat = this.covertDegree(locat[0], (locat[0].charAt(locat[0].length - 1)));
                        let lon = this.covertDegree(locat[1], (locat[1].charAt(locat[1].length - 1)));
                        this.addMarkers(new L.LatLng(lat, lon));
                        this.markers.push(new L.LatLng(lat, lon));
                    }

                }
            }

        },

        mapClick(ev) {
            this.isDouble = false;
            setTimeout(() => {
                if (this.isDouble) return;
                this.addMarkers(ev.latlng);
                this.markers.push(ev.latlng);
            }, 300);
        },

        mapdbClick(ev) {
            this.isDouble = true;
            this.map.removeLayer(ev.target);
            for (let i = 0; i < this.markers.length; i++) {
                if (this.markers[i].lat == ev.latlng.lat && this.markers[i].lng == ev.latlng.lng) {
                    this.markers.splice(i, 1);
                    break;
                }
            }
        },

        addMarkers(point) {
            let myIcon = L.icon({ iconUrl: 'static/assets/icons/marker-icon.png', iconSize: [30, 30], popupAnchor: [-3, -76], });
            let marker = L.marker(point, { icon: myIcon });
            marker.on("dblclick", this.mapdbClick);
            this.map.addLayer(marker);
        },

        // 界面相关
        cancel() {
            this.$emit('cancel');
        },
        save() {
            //转为度分秒格式保存
            let coordinateData = "";
            this.markers.forEach((item) => {
                coordinateData += this.formatDegreeBack(item.lat, 'lat') + ',' + this.formatDegreeBack(item.lng, 'lon') + ';';
            });
            this.$emit('save', coordinateData);
        },


    },
    created() {
        // this.map.on("click", this.mapClick);
    },
    mounted() {
        this.$nextTick(function() {
            this.initMap();
        })

    }
}
</script>


