<template>
    <div :class="style.backcolor">
        <div id="mydiv" :class="style.convas"></div>
        <div>
           <!-- <div :class="style.title">
           </div> -->
           <!-- <div class="login-form"> -->
                <log-form :class="style.form" ref="form"></log-form>
           <!-- </div> -->
        </div>
       <!--  <div :class="style.footer">
            <div><a href="http://www.gly.cn/Index.htm">主办单位：外交部边海司</a></div>
            <div :class="style.space"><a href="http://www.geo-compass.com/">技术支持：国信司南（北京）地理信息技术有限公司</a></div>
        </div> -->
    </div>
</template>
<script>
// import CanvasParticle from 'canvas-particle'
import style from '@/css/login.css'
import {login,getSession,SET_USER} from './mutation-types'
import {mapActions} from 'vuex'
import LogForm from './form'
export default {
   name:'login',
   data(){
       return {
           style,
           config:{
            vx: 4,  //小球x轴速度,正为右，负为左
            vy: 4,  //小球y轴速度
            height: 2,  //小球高宽，其实为正方形，所以不宜太大
            width: 2,
            count: 500,   //点个数
            color: "121, 162, 185",   //点颜色
            stroke: "130,255,255",    //线条颜色
            dist: 6000,   //点吸附距离
            e_dist: 20000,  //鼠标吸附加速距离
            max_conn: 10  //点到点最大连接数
           }
       };       
   },
   components:{
       LogForm
   },
   methods:{
       ...mapActions({
           login,
           getSession
       })
   },
   created(){

   },
   mounted(){
       //调用
    console.log(this.config);
        CanvasParticle(this.config);

   }
}
</script>




