import { sysClient } from '@/clients/hprose-client'
import * as Types from './mutation-types'
import QueryParam from '@/models/business/query-param'

const state = {
    queryParam: new QueryParam()
}

const getters = {
    [Types.queryParam]: ({ queryParam }) => queryParam
}

const mutations = {
    [Types.SET_QUERYPARAM]: (state, queryParam) => state.queryParam = queryParam
}

const actions = {
    [Types.seeArticleData]: (context, args) => {
        return sysClient.getData('seeArticleData', args);
    },
    [Types.putArticleData]: (context, args) => {
        return sysClient.getData('putArticleData', args);
    },
    [Types.edittArticleData]: (context, args) => {
        return sysClient.getData('edittArticleData', args);
    },
    [Types.getRecords]: (context, args) => {
        // 数据查询
        return sysClient.getData('getArticleDataList', args);
    },
    [Types.delData]: (context, args) => {
        //删除或批量删除
        return sysClient.getData('deleteArticleData', args,true);
    },
    [Types.getCompanyData]: (context,args)=>sysClient.getData('getCompanyList', args, false),
    [Types.getLanguageList]: (context,args)=>sysClient.getData('getLanguage', args, false),
    [Types.getPeriodList]:(context,args)=>sysClient.getData('getPeriod', args, false),
    [Types.signArticleData]:(context,args)=>sysClient.getData('signArticleData', args, false),
    [Types.publishData]:(context,args)=>sysClient.getData('PublishArticleData',args,true),
    [Types.getEnclPath]:(context,args)=>sysClient.getData('getArticlePath',args,false),
    [Types.exportData]:(context,args)=>sysClient.getData('ExportArticleData',args,true),
    [Types.importData]:(context,args)=>sysClient.getData('ImportArticleData',args,true),
}
export default {
    state,
    getters,
    mutations,
    actions
}





