let checkname = (rule, value, callback) => {
    if (!value) {
        callback(new Error('请填写名称'));
        return;
    }
    else {
        callback();
    }
};
let checkNAME = (rule, value, callback) => {
    if (!value) {
        callback(new Error('请填写用户名'));
        return;
    }
    else {
        callback();
    }
};
let checkREALNAME = (rule, value, callback) => {
    if (!value) {
        callback(new Error('请填写用户真实姓名'));
        return;
    }
    else {
        callback();
    }
};
let checkPASSWORD = (rule, value, callback) => {
    if (!value) {
        callback(new Error('请填写用户密码'));
        return;
    }
    else {
        callback();
    }
};
let checkMS = (rule, value, callback) => {
    if (!value) {
        callback();
        return;
    }
    if (value.length > 500) {
        callback(new Error(' '));
        return;
    } else {
        callback();
    }
};
let checkTGDW = (rule, value, callback) => {
    if (!value) {
        callback(new Error(' '));
        return;
    }
    else {
        callback();
    }
};
let checkTGDWZB = (rule, value, callback) => {
    if (!value) {
        callback(new Error(' '));
        return;
    }
    else {
        callback();
    }
};
let checkkeyWords = (rule, value, callback) => {
    if (!value) {
        callback(new Error('请填写关键词'));
        return;
    }
    else {
        callback();
    }
};
let checkProduceTime = (rule, value, callback) => {
    if (value == null || isNaN(Number(value)) || value.toString().length > 4) {
        callback(new Error(' '));
    } else {
        callback();
    }
};
let checkXZSJ = (rule, value, callback) => {
    if (value == null || isNaN(Number(value)) || value.toString().length > 4) {
        callback(new Error(' '));
    } else {
        callback();
    }
};
let checkYYID = (rule, value, callback) => {
    if (!value || value.length == 0) {
        callback(new Error('请选择语言'));
        return;
    }
    else {
        callback();
    }
};
let checkSLT = (rule, value, callback) => {
    if (!value || value == "") {
        callback(new Error('请上传缩略图！'));
        return;
    }
    else {
        callback();
    }
};
