<template>
    <el-dialog class="author" title="关键词" :visible="keywordDialogVisible"  :before-close="cancel" size="tiny">
       <div :index="index" :item="item" v-for="(item,index) in formData" :key="`${index}`" @del="del"  class="flex-container">
            <span style="width:100px">关键词:</span>
            <el-input  v-model="item.GJC"></el-input>
            <i class="el-icon-minus" @click="del(index)" v-if="keywordFooterVisible"></i>
            <i class="el-icon-plus" @click="add" v-if="(index===formData.length-1)&&keywordFooterVisible"></i>
            <i style="padding:12px" v-else></i>
        </div>     
        <i class="el-icon-plus" style=" float: left;margain-left:0px" v-if="!formData.length" @click="add"> 新增关键词</i>   
        <div slot="footer" class="dialog-footer" v-if="keywordFooterVisible">
            <el-button type="primary" @click="save">确 定</el-button>
            <el-button @click="cancel">取 消</el-button>
        </div>
    </el-dialog>
</template>
<script>
export default {
    data() {
        return {
            formData: [{ GJC: '' }],
        }
    },
    watch: {
        keywordData() {
            let arr = this.authorMsgStr2Array(this.keywordData);
            this.formData = arr;
        }
    },
    props: {
        keywordDialogVisible: Boolean,
        keywordData: String,
        keywordFooterVisible:Boolean,
    },
    methods: {
        authorMsgStr2Array(str) {
            let result = [];
            let msgArr=[];
            if (!str) return result;

            if (str.indexOf(',') <= 0) {
                msgArr.push(str);
            } else {
                msgArr = str.split(",");
            }

            msgArr.forEach(function (item) {
                result.push({ GJC: item });
            });
            return result;
        },
        add() {
            this.formData.push({ GJC: '' });
        },
        del(index) {
            let temp = this.formData;
            temp.splice(index, 1);
            this.formData = temp;
        },
        save() {
            //组织成字符串
            let msg = "";
            this.formData.forEach(function (item) {
                msg += item.GJC + ',';
            });
            msg=msg.slice(0,msg.length-1);
            this.$emit('save', msg);
        },
        cancel() {
            this.$emit('cancel');
        }
    }
}
</script>




