<template>
    <div :class="style.layout">
        <app-header></app-header>
        <app-menu :class='style.menu'></app-menu>
        <div :class='style.content'>
            <breacrumb :class='style.bread'></breacrumb>
            <router-view :class='style.view'></router-view>
        </div>
    </div>
</template>
<script>
import style from '@/css/main.css'
import AppHeader from '#com/header'
import AppMenu from '#com/menu'
import Breacrumb from '#com/breadcrumb'
export default {
    name: "main",
    data() {
        return {
            style
        }
    },
    components: {
        AppHeader,
        AppMenu,
        Breacrumb,
    }
}
</script>




