<template>
    <div>
        <!-- <excel-export></excel-export> -->
        <common-grid :tableData="tableData" :collums="collums" :total="total" @read="viewDetail" @add="add" @search="changeQueryParam" @del="delDatas"
         @edit="edit" @toSpecial="toSpecialItem" @publish="publish"  @downLoad="downLoad" @exportData="exportDatafun" @uploadSuccessful="uploadSuccessful">
        </common-grid>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { getRecords } from './mutation-types'
import { delData,setMonSpeList,publishData,getEnclPath,exportData,importData } from './mutation-types'
import collums from '@/data/treatise-grid-header'
import CommonGrid from '#com/common-grid'
import ExcelExport from '#com/excel-export'
import QueryParam from '@/models/business/query-param'
import FormRouteQuery from '@/models/business/form-route-query'
import * as  EditState from '@/enums/edit-state'
import { DOWNLOADURL } from '@/configs/upload-download'
export default {
    name: 'treatise-grid',
    data() {
        return {
            tableData: null,
            collums,
            total: 0,
            queryParam: new QueryParam()
        }
    },
    methods: {
        viewDetail(index, row) {
            this.$router.push({
                name: 'treatise-form',
                query: new FormRouteQuery(row.ID, EditState.READ, row.SPMC)
            })
        },
        changeQueryParam(param) {
            this.queryParam = param;
            this.getData();
        },
        async getData() {
            let result = await this.getRecords(Object.values(this.queryParam));
            this.tableData = result.list;
            this.total = result.count;
        },
          //导入附件上传成功后回调
       async uploadSuccessful(load){
             let result = await this.importData(load);
             this.$message({
                showClose: true,
                message: result.result,
                type:result.status?'success':'error',
            });
            if (result.status) this.getData();
        },
         //导出
        async exportDatafun(expdata,keyword) {
            let result = await this.exportData([JSON.stringify(expdata),keyword]);
            var a = document.createElement('a'); 
            a.download = "";
            a.style.display = 'none';
            a.href = DOWNLOADURL+result;
            a.click();
        },
        ...mapActions({
            getRecords,
            delData,
            setMonSpeList,
            publishData,
            getEnclPath,
            exportData,
            importData
        }),
        add() {
            this.$router.push({
                name: 'treatise-form',
                query: { state: EditState.ADD }
            })
        },
        async delDatas(datas) {
            //此处调用删除数据逻辑，datas为删除或批量删除的数据数组
            let result = await this.delData(JSON.stringify(datas));

            this.$message({
                showClose: true,
                message: result.result,
            });

            if (result.status === true) {
                this.getData();
            }
        },
        //编辑
        edit(index, row) {
            this.$router.push({
                name: 'treatise-form',
                query: new FormRouteQuery(row.ID, EditState.EDIT, row.SPMC)
            });
        },
        async toSpecialItem(param) {
            let result = await this.setMonSpeList(param);
            this.$message({
                showClose: true,
                message: result.result,
            });
            if (result.status) this.getData();

        },
        async publish(vidData,isTrue){
            let result = await this.publishData([JSON.stringify(vidData),isTrue]);
           this.$message({
                showClose: true,
                message: result.result,
                type:result.status?'success':'error',
            });
            if (result) this.getData();
        },    
        async downLoad(id) {
            let result = await this.getEnclPath(id);
            if (!result || !result.count) { alert("暂无附件"); return; };
            let files = result.list;
            files.forEach(function(element) {
                this.downLoadFile(element.FJLJ);
            }, this);
        },
        downLoadFile(filename) {
            let filepath=DOWNLOADURL+filename;
            var a = document.createElement('a');
            a.download = "";
            a.style.display = 'none';
            a.href = filepath;// 模拟a标签点击进行下载
            a.click();
        },
    },
    components: {
        CommonGrid,
        ExcelExport
    },

    created() {
        this.getData();
    }
}
</script>




