import Treatise from './'
import Grid from './grid'
import Form from './form'
export default {
    name: 'treatise',
    path: 'treatise',
    meta:{alias:'专著'},
    component: Treatise,
    redirect: {name:'treatise-grid'},
    subRoutes: [
        {
            name: 'treatise-grid',
            path: 'grid',
            meta:{alias:'表格',isForm:false,moduleDesc:'专著',dataDesc:''},
            component: Grid
        },
        {
            name: 'treatise-form',
            path: 'form',
            meta:{alias:'表单',isForm:true},
            component: Form
        }
    ]
}


