<template>
    <div>
        <!-- <excel-export></excel-export> -->
        <common-grid :tableData="tableData" :downloadVis="downloadVis" :collums="collums" :total="total" @read="viewDetail" @add="add" @search="changeQueryParam" @del="delDatas" @edit="edit">
        </common-grid>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { getRecords, delData } from './mutation-types'
// import { delData } from './mutation-types'
import collums from '@/data/user-grid-header'
import CommonGrid from '#com/common-grid'
import QueryParam from '@/models/business/query-param'
import * as  EditState from '@/enums/edit-state'
import FormRouteQuery from '@/models/business/form-route-query'
import { user } from '@/components/modules/login/mutation-types'
import { mapGetters } from 'vuex'
export default {
    name: 'user-grid',
    data() {
        return {
            tableData: null,
            collums,
            total: 0,
            queryParam: new QueryParam(),
            downloadVis: false,
            EditState,
        }
    },
    methods: {
        viewDetail(index, row) {
            this.$router.push({
                name: 'user-form',
                query: new FormRouteQuery(row.ID, EditState.READ)
            })
        },
        changeQueryParam(param) {
            this.queryParam = param;
            this.getData();
        },
        async getData() {
            let result = await this.getRecords(Object.values(this.queryParam));
            this.tableData = result.list;
            this.total = result.count;
        },
        ...mapActions({
            getRecords,
            delData,
        }),
        add() {
            this.$router.push({
                name: 'user-form',
                query: { state: EditState.ADD }
            });
        },
        async delDatas(datas) {
            //此处调用删除数据逻辑，datas为删除或批量删除的数据数组
            let result = await this.delData(JSON.stringify(datas));

            if (result.msg == 'fail') {
                this.$message({
                    message: result.result,
                    type: 'warning'
                });
            }
            if (result.msg == 'success') {
                this.$message({
                    message: result.result,
                    type: 'info'
                });
                this.getData();
            }
        },

        //编辑
        edit(index, row) {
            this.$router.push({
                name: 'user-form',
                query: new FormRouteQuery(row.ID, EditState.EDIT)
            });
        },

    },
    components: {
        CommonGrid,
    },
    computed: {
        ...mapGetters({
            user
        })
    },
    created() {
        let param = new QueryParam();
        this.queryParam = param;
        this.getData();
    }
}
</script>




