<template>
	<div class="login-checkcode" @click='changeCheckcode'>
		<canvas id="checkcodecanvas" :width="width" :height="height"></canvas>
	</div>
</template>
<script>
	export default {
		name: 'login-checkcode',
		/* 组件数据对象 */
		data: function() {
			return {};
		},

		/* 使用 props传递数据 */
		props: {
			width: {
				type: Number,
				default: 100,
			},
			height: {
				type: Number,
				default: 40,
			}
		},

		/* 添加组件方法 */
		methods: {
			changeCheckcode() {
				let code = this.generateCheckCode();
				this.$emit('changed', code);
			},
			/**生成一个随机数**/
			randomNum(min, max) {
				return Math.floor(Math.random() * (max - min) + min);
			},
			/**生成一个随机色**/
			randomColor(min, max) {
				var r = this.randomNum(min, max);
				var g = this.randomNum(min, max);
				var b = this.randomNum(min, max);
				return "rgb(" + r + "," + g + "," + b + ")";
			},
			generateCheckCode() {
				var canvas = document.getElementById("checkcodecanvas");
				var width = canvas.width;
				var height = canvas.height;
				var ctx = canvas.getContext('2d');
				ctx.textBaseline = 'bottom';
				/**绘制背景色**/
				ctx.fillStyle = "rgb(243,251,254)"; //颜色若太深可能导致看不清
				ctx.fillRect(0, 0, width, height);
				/**绘制文字**/
				var str = 'ABCEFGHJKLMNPQRSTWXY123456789';
				var checkCode="";
				for(var i = 0; i < 4; i++) {
					var code = str[this.randomNum(0, str.length)];
					checkCode+=code;
					ctx.fillStyle = "rgb(84,148,22)"; //随机生成字体颜色
					ctx.font = '200 '+ this.randomNum(30, 40) + 'px SimHei'; //随机生成字体大小
					var x =10+i * 20;
					var y = 8+this.randomNum(29, 34);
					var deg = this.randomNum(-15, 15);
					//修改坐标原点和旋转角度
					ctx.translate(x, y);
					ctx.rotate(deg * Math.PI / 180);
					ctx.fillText(code, 0, 0);
					//恢复坐标原点和旋转角度
					ctx.rotate(-deg * Math.PI / 180);
					ctx.translate(-x, -y);
				}
				/**绘制干扰线**/
				// for(var i = 0; i < 8; i++) {
				// 	ctx.strokeStyle = this.randomColor(40, 180);
				// 	ctx.beginPath();
				// 	ctx.moveTo(this.randomNum(0, width), this.randomNum(0, height));
				// 	ctx.lineTo(this.randomNum(0, width), this.randomNum(0, height));
				// 	ctx.stroke();
				// }
				/**绘制干扰点**/
				for(var i = 0; i < 100; i++) {
					ctx.fillStyle = this.randomColor(0, 255);
					ctx.beginPath();
					ctx.arc(this.randomNum(0, width), this.randomNum(0, height), 1, 0, 2 * Math.PI);
					ctx.fill();
				}
				return checkCode.toLowerCase();
			}
		},

		mounted() {
			this.changeCheckcode();
		},
	}
</script>
<style scoped>
	.login-checkcode {
		/* Positioning */
		border-width: 1px;
		/* Typography */
		/* Visual */
		border-color: #cccccc;
		/* Misc */
	}
</style>


