import { SET_HASLOINGED,SET_USER } from '@/components/modules/login/mutation-types'
export default (router, store)=>{
	router.beforeEach((to, from, next) => {
		if (to.path == '/login') {
			next();
			return;
		}		
		if (store.state.login.hasLogined) {
			next();
		} else {		
			store.dispatch('login/getSession').then(result => {				
				if(Boolean(result.status)){
					store.commit(SET_USER,result.result);
					store.commit(SET_HASLOINGED,true);
					to.path == '/'?next('/main'):next()
				}else
				    next('/login');										
			}, error => {
				next('/login');
			});
		}
	});
}



