<template>
    <div :class="style.basicinf">
        <div :class="style.form">
            <div :class="style.basicinfLeft">基本信息</div>
            <div :class="style.basicinfRight">
                <el-form ref="formData" :model="formData" :rules="rules">
                    <div :class="style.list_row">
                        <div :class="style.list_cell">
                            <div>
                                <div :class="style.field">影像名称</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="DTMC">
                                        <el-input :readonly="state==EditState.READ" class="vali" v-model="formData.DTMC"></el-input>
                                    </el-form-item>

                                </div>
                            </div>

                            <div>
                                <div :class="style.field">电子文件名称</div>
                                <div :class="style.fieldValue">
                                    <el-input readonly class="content" v-model="formData.DZWJMC" @focus="editDZWJMC"></el-input>
                                    <i class="el-icon-edit" @click="editDZWJMC"></i>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">影像来源</div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.YXLY"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">影像生成时间</div>
                                <div :class="style.fieldValue">
                                    <!-- <el-input :readonly="state==EditState.READ" class="content" v-model="formData.YXSCSJ"></el-input> -->
                                    <el-date-picker class="dateEditor" :readonly="state==EditState.READ" type="date" placeholder="选择日期" v-model="formData.YXSCSJ"></el-date-picker>

                                </div>
                            </div>
                            <div>
                                <div :class="style.field">提供单位</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="TGDWMC">
                                        <!-- <el-select class="content" v-model="formData.TGDWID" placeholder="请选择提供单位" /> -->
                                        <el-autocomplete class="inline-input-vali" :disabled="state==EditState.READ" v-model="state2" :fetchSuggestions="querySearch" placeholder="请输入内容" @select="handleSelect" :trigger-on-focus="false">

                                        </el-autocomplete>
                                    </el-form-item>

                                </div>
                            </div>
                            <div>
                                <div :class="style.field">提供单位坐标</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="TGDWZB">
                                        <el-input readonly class="vali" v-model="formData.TGDWZB" @focus="editCompCoordinate"></el-input>
                                        <i class="el-icon-edit" @click="editCompCoordinate"></i>
                                        <i class="pointPickup" @click="toPickup"></i>
                                    </el-form-item>

                                </div>
                            </div>
                        </div>
                        <div :class="style.list_cell">

                            <div>
                                <div :class="style.field">语言</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="YYID">
                                        <el-select multiple class="vali" :disabled="state==EditState.READ" v-model="formData.YYID">
                                            <el-option v-for="(item,index) in languageList" :key="`${index}`" :value="item.ID" :label="item.MC"></el-option>
                                        </el-select>
                                    </el-form-item>

                                </div>
                            </div>

                            <div>
                                <div :class="style.field">时期范围</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SQFWID">
                                        <el-select :disabled="state==EditState.READ" class="vali" v-model="formData.SQFWID">
                                            <el-option v-for="(item,index) in periodList" :key="`${index}`" :value="item.ID" :label="item.SQFW"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">关键词</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="GJC">
                                        <el-input readonly class="vali" placeholder="多个关键词请用逗号分隔" v-model="formData.GJC" @focus="editKeyword"></el-input>
                                        <i class="el-icon-edit" @click="editKeyword"></i>
                                    </el-form-item>
                                </div>

                            </div>
                            <div>
                                <div :class="style.field">所在洲、国家</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SZZID">
                                        <el-cascader class="vali" :value="value" :disabled="state==EditState.READ" placeholder="" :options="regionList" filterable change-on-select @change="localHandleChange"></el-cascader>
                                    </el-form-item>

                                </div>
                            </div>
                            <!-- <div>
                                <div :class="style.field">定位坐标</div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.DWZB"></el-input>
                                </div>
                            </div> -->
                            <div>
                                <div :class="style.field">受控度</div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SKD">
                                        <el-select class="vali" :disabled="state==EditState.READ" v-model="formData.SKD">
                                            <el-option v-for="(item,index) in skdList" :key="`${index}`" :value="item.id" :label="item.name"></el-option>
                                        </el-select>
                                    </el-form-item>

                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <!-- <el-input :readonly="state==EditState.READ" class="content" v-model="formData.DWZB"></el-input> -->
                                </div>
                            </div>
                        </div>
                        <div :class="style.list_cell">
                            <div>
                                <div :class="style.field">空间分辨率</div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.KJFBL"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">影像坐标</div>
                                <div :class="style.fieldValue">
                                    <!-- <el-input :readonly="state==EditState.READ" class="content" v-model="formData.YXZB"></el-input> -->
                                    <el-input class="content" readonly v-model="formData.YXZB" @focus="editCoordinate"></el-input>
                                    <i class="el-icon-edit" @click="editCoordinate"></i>
                                    <i class="multipointPickup" @click="toMultiPickup"></i>
                                </div>
                            </div>

                            <div>
                                <div :class="style.field">所处位置</div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.SCWZ"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field">网址</div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.DTWZ"></el-input>

                                </div>
                            </div>
                            <div>
                                <div :class="style.field">附件地址</div>
                                <div :class="style.fieldValue">
                                    <el-input v-if="isVisable" :readonly="this.state==EditState.READ" class="content" placeholder="请输入内容" v-model="formData.FJLJ">
                                        <template slot="prepend">{{url}}</template>
                                    </el-input>
                                    <el-input v-else class="content" readonly v-model="formData.FJLJ">
                                    </el-input>

                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">

                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div>
                            <div :class="style.fieldBottom">描述</div>
                            <div :class="style.fieldValueBottom">
                                <el-input :readonly="state==EditState.READ" class="fieldValueBottom" type="textarea" v-model="formData.MS" placeholder="说明该资料的时间、地点、作者、位置、时间、原因；该图中所发现信息的重点提示。 比如：该图中：钓鱼岛、赤尾屿用数字代号加脚注形式表示 "></el-input>
                            </div>
                        </div>
                        <div>
                            <div :class="style.fieldBottom">备注</div>
                            <div :class="style.fieldValueBottom">
                                <el-input :readonly="state==EditState.READ" class="fieldValueBottom" type="textarea" placeholder="关于资料需要额外说明信息" v-model="formData.BZ"></el-input>
                            </div>
                        </div>
                        <div>
                            <div :class="style.pic">缩略图</div>
                            <div :class="style.picBottom">
                                <!-- <div>img</div> -->
                                <!-- <el-button :class="style.picbtn" type="success">上传缩略图</el-button> -->
                                <el-upload class="avatar-uploader" v-if="showSLT" :disabled="state==EditState.READ" :action="UPLOADURL" :show-file-list="false" :on-success="handleAvatarSuccess">
                                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                        </div>
                    </div>
                </el-form>
            </div>
        </div>
        <div :class="style.btndiv" v-if="state!==EditState.READ">
            <el-button class="sureBtn" @click="validate('formData')">
                确定
            </el-button>
            <el-button class="cancelBtn" @click="cancelBtnHandler('formData')">
                取消
            </el-button>
        </div>
        <map-coordinate :mapCoordinateData="mapCoordinateData" :dialogFormVisible="locFormVisible" @save="save" @cancel="cancelbtn" :mapCoorFooterVisible="mapCoorFooterVisible"></map-coordinate>
        <company-Coordinate :mapCoordinateData="companyLoc" :dialogFormVisible="companylocVisible" @save="savebtn" @cancel="cancelbtn" :mapCoorFooterVisible="mapCoorFooterVisible"></company-Coordinate>
        <keyword-dialog :keywordDialogVisible="keywordDialogVisible" :keywordData="keywordData" @save="saveKeyword" @cancel="cancelKeyword" :keywordFooterVisible="keywordFooterVisible"></keyword-dialog>
        <electronfile-dialog :electronfiledDialogVisible="electronfiledDialogVisible" :electronFileData="electronFileData" :electronFileFooterVisible="electronFileFooterVisible" @save="saveElectronfile" @cancel="cancelElectronfile"></electronfile-dialog>
        <point-pickup v-if="pickupFormVisible" :point="point" :allowPickup="allowPickup" @cancel="cancelbtn" @save="savebtn"></point-pickup>
        <point-pickup-multi v-if="multiPickupFormVisible" :points="points" :allowPickup="allowPickup" @cancel="cancelbtn" @save="save"></point-pickup-multi>
    </div>
</template>

<script>
import { DOWNLOADURL, UPLOADURL } from '@/configs/upload-download'
import style from '@/css/image-form.css'
import * as  EditState from '@/enums/edit-state'
import OM_IMAGE from '@/models/entity/OM_IMAGE'
import { addRecords, editRecords, getFormData, getCompanyData, getLanguageList, getPeriodList, getRegionList } from './mutation-types'
import { mapActions, mapMutations } from 'vuex'
import FormRouteQuery from '@/models/business/form-route-query'
import companyCoordinate from '#com/company-coordinate'
import MapCoordinate from '#com/map-coordinate'
import FileAddress from '#com/file-address'
import { rules } from '@/util/rules.js'
import KeywordDialog from '#com/keyword-dialog'
import electronfileDialog from '#com/electronfile-dialog'
import PointPickup from '#com/point-map-pickup'
import PointPickupMulti from '#com/point-map-pickup-multi'
export default {
    name: 'image-form',
    data() {
        return {
            style,
            labelPosition: 'right',
            state: null,
            EditState,
            formData: new OM_IMAGE(),
            tmList: [{ id: '0', name: '否' }, { id: '1', name: '是' }],
            skdList: [{ id: '0', name: '公开' }, { id: '1', name: '受控' }],
            companyListData: [],
            languageList: [],
            periodList: [],
            regionList: [],
            state2: '',
            dataID: null,
            initData: null,
            mapCoordinateData: null,
            companyLoc: null,
            companylocVisible: false,
            mapCoorFooterVisible: true,
            locFormVisible: false,
            //附件地址相关
            fileaddressData: null,
            fileDialogVis: false,
            currentName: 'image',
            url: '',
            rules: rules,
            isVisable: true,
            value: [],
            //缩略图相关
            DOWNLOADURL,
            UPLOADURL,
            imageUrl: '',
            imgPath: '',
            showSLT: true,
            //关键词组件可见性及数据源
            keywordDialogVisible: false,
            keywordData: "",
            keywordFooterVisible: true,
            //电子文件名称组件可见性及数据源等
            electronfiledDialogVisible: false,
            electronFileData: "",
            electronFileFooterVisible: true,
            //坐标拾取相关
            pickupFormVisible: false,
            point: null,
            allowPickup: true,
            multiPickupFormVisible: false,
            points: null,

        }
    },
    components: {
        companyCoordinate,
        FileAddress,
        MapCoordinate,
        KeywordDialog,
        electronfileDialog,
        PointPickup,
        PointPickupMulti,
    },
    methods: {

        ...mapActions({
            addRecords,
            editRecords,
            getFormData,
            getCompanyData,
            getLanguageList,
            getPeriodList,
            getRegionList
        }),

        initForm() {
            this.initData = this.$route.query;
            this.state = this.initData.state;
            // if (this.state == EditState.READ)
            //     this.allowPickup = false;
            if (this.state !== EditState.ADD) {
                this.dataID = this.initData.id;
                this.getDataDetail();
            }
        },
        async getDataDetail() {
            let result = await this.getFormData(this.initData.id);
            if (!result) return;
            let form = result;
            if (form && form.hasOwnProperty('TGDWMC') && form.hasOwnProperty('TGDWZB')) {
                this.state2 = form.TGDWMC;
                this.point = form.TGDWZB;
                //需要转一下获取的TGDWZB，将十进制转成度分秒
                if (form.TGDWZB) {
                    let arr = form.TGDWZB.split(',');
                    form.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
                }
            }
            if (form.hasOwnProperty('YXZB')) {
                this.points=form.YXZB;
            }
            //转一下洲，国家，显示
            if (form.SZZID && form.SZGJID) {
                this.value = [form.SZZID, form.SZGJID];
            } else if (form.SZZID && !form.SZGJID) {
                this.value = [form.SZZID];
            }
            // this.formData = new OM_IMAGE(form);
            if (!form.FJLJ) {
                this.isVisable = true;
            } else {
                this.isVisable = false;
            }
            if (form.YYID)
                form.YYID = form.YYID.map(item => item.ID);
            if (form.SLT)
                this.imageUrl = this.DOWNLOADURL + form.SLT;
            if (form.SLT == "" && this.state == EditState.READ)
                this.showSLT = false;
            this.formData = form;

        },

        querySearch(queryString, cb) {
            var companyListData = this.companyListData;
            var results = queryString ? companyListData.filter(this.createFilter(queryString)) : companyListData;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
            return (companyListData) => {
                if (companyListData.value) {
                    return (companyListData.value.indexOf(queryString.toLowerCase()) !=-1);
                }
            };
        },
        addData() {
            if (!this.formData) return;
            this.savedata();
        },
        async savedata() {
            let form = new OM_IMAGE(this.formData);
            if (!form) return;
            if (form.FJLJ != ""&&this.isVisable) {
                form.FJLJ = this.url + form.FJLJ;
            }
            if (form.YXSCSJ) {
                form.YXSCSJ = this.transDate2DatetimeStr(form.YXSCSJ).split(' ')[0];
            }


            //处理一下中文逗号分号
            form.GJC = form.GJC.replace(/，/ig, ',');
            form.DZWJMC = form.DZWJMC.replace(/；/ig, ';');
            //TGDWZB转成十进制，经度在前纬度在后
            if (form.TGDWZB) {
                let arr = form.TGDWZB.split(",");
                let lon = arr[1].slice(arr[1].length - 1, arr[1].length);
                let lat = arr[0].slice(arr[0].length - 1, arr[0].length)
                form.TGDWZB = this.covertDegree(arr[1], lon) + ',' + this.covertDegree(arr[0], lat);
            }
            // if (form.TGDWMC && this.state2) {
            //     if (form.TGDWMC != this.state2) {
            //         form.TGDWMC = this.state2;
            //         form.TGDWID = 0;
            //     }
            // }
            if (!this.state2) {
                form.TGDWMC = "未知单位";
                form.TGDWID = 1;
                form.TGDWZB = "";
            }
            if (form.YYID) {
                form.YYID = this.createyyFilter(form.YYID);

            }
            form.SLT = this.imgPath;
            let formData = JSON.stringify(form);

            let result;
            if (this.dataID !== null)
                result = await this.editRecords(formData);
            else
                result = await this.addRecords(formData);
            if (result.status) {
                this.showSuccess();
                this.$router.push({ name: 'image-grid' });
            }
            else
                this.showError();
        },
        validate(formName) {
            this.formData.SLT = this.imgPath;
            let val;
            this.$refs[formName].validate((valid) => {
                val = valid;
                if (valid) {
                    this.savedata();
                } else {
                    return false;
                }
            });
            if (val) return;
            this.$notify({
                title: '提示',
                message: "请填写必填项",
                duration: 3000,
                type: 'info',
                offset: 125,
            });
        },
        handleSelect(item) {
            // this.formData.TGDWID = item.id;
            // this.formData.TGDWMC = item.value;
            // // this.formData.TGDWZB = item.loc;
            // //转一下item.loc为度分秒的
            // let arr = item.loc.split(',');
            // this.formData.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
            // this.mapCoordinateData = this.formData.TGDWZB;
            this.focusout();
        },
        focusout() {
            if (!this.state2) { this.formData.TGDWZB = ""; return; }
            let currenttgdw = this.companyListData.find(item => item.value == this.state2);
            if (!currenttgdw) {
                this.formData.TGDWMC = this.state2;
                this.formData.TGDWID = 0;
            }
            else {
                this.formData.TGDWMC = this.state2;
                this.formData.TGDWID = currenttgdw.id;
                if (currenttgdw.loc) {
                    let arr = currenttgdw.loc.split(',');
                    this.formData.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
                }
            }
        },
        localHandleChange(item) {
            //解析item分别赋给this.formData.SZZID;this.formData.GJID
            if (item.length == 1) {
                this.formData.SZZID = item[0];
                this.formData.SZGJID = "";
            } else if (item.length == 2) {
                this.formData.SZZID = item[0];
                this.formData.SZGJID = item[1];
            }
        },
        async getcompanyList() {
            let companyData = await this.getCompanyData();
            if (!companyData) return;
            this.companyListData = companyData.map(item => {
                return { value: item.MC, id: item.ID, loc: item.TGDWZB };
            });
            // this.companyListData = companyData;
        },
        //获取语言列表
        async getlanguageData() {
            let languageData = await this.getLanguageList();
            if (!languageData) return;
            this.languageList = languageData;
        },
        createyyFilter(queryArray) {
            let filterlgArr = [];
            for (let i = 0; i < queryArray.length; i++) {
                let id = queryArray[i];
                for (let m = 0; m < this.languageList.length; m++) {
                    if (this.languageList[m].ID == id) {
                        filterlgArr.push(this.languageList[m]);
                    }

                }
            }
            return filterlgArr;
        },
        //获取时期范围
        async getPeriodData() {
            let periodData = await this.getPeriodList();
            if (!periodData) return;
            this.periodList = periodData;
        },
        //获取洲，国家
        async getRegionData() {
            let result = await this.getRegionList();
            // console.log(regionData);
            if (!result) return;
            let regionData = result;
            regionData.forEach(function(item) {
                if (item.hasOwnProperty('CHILDREN')) {
                    item.CHILDREN = item.CHILDREN.map(item1 => {
                        return { value: item1.ID, label: item1.MC };
                    });
                }
            });
            this.regionList = regionData.map(item => {
                return { value: item.ID, label: item.MC, children: item.CHILDREN };
            });
            // console.log(this.regionList);
        },
        //成功提示
        showSuccess() {
            this.$message({
                message: '保存成功',
                type: 'success'
            });
        },
        //保存不成功提示
        showError() {
            this.$message({
                message: '保存失败',
                type: 'warning'
            });
        },
        //取消
        cancelBtnHandler(formName) {
            this.formData = new OM_IMAGE();
            this.$refs[formName].resetFields();
        },
        //编辑地图关注点坐标信息
        editCoordinate() {
            this.mapCoordinateData = this.formData.YXZB;
            if (this.state == EditState.READ)
                this.mapCoorFooterVisible = false;
            else
                this.mapCoorFooterVisible = true;
            this.locFormVisible = true;
        },
        //编辑提供单位坐标
        editCompCoordinate() {
            if (this.state == EditState.READ) this.mapCoorFooterVisible = false;
            else this.mapCoorFooterVisible = true;
            this.companylocVisible = true;
            this.companyLoc = this.formData.TGDWZB;
        },
        //编辑坐标工具栏的确定和取消
        cancelbtn() {
            this.companylocVisible = false;
            this.locFormVisible = false;
            this.pickupFormVisible = false;
            this.multiPickupFormVisible = false;
        },
        //编辑关键词
        editKeyword() {
            if (this.state == EditState.READ)
                this.keywordFooterVisible = false;
            else
                this.keywordFooterVisible = true;
            this.keywordDialogVisible = true;
            this.keywordData = this.formData.GJC;
        },
        //保存关键词
        saveKeyword(gjcdata) {
            this.keywordDialogVisible = false;
            if (!gjcdata) return;
            this.formData.GJC = gjcdata;

        },
        //关闭关键词dialog
        cancelKeyword() {
            this.keywordDialogVisible = false;
        },
        //编辑电子文件名称
        editDZWJMC() {
            if (this.state == EditState.READ) this.electronFileFooterVisible = false;
            else this.electronFileFooterVisible = true;
            this.electronfiledDialogVisible = true;
            this.electronFileData = this.formData.DZWJMC;
        },
        //保存电子文件名称
        saveElectronfile(dzwjmcdata) {
            if (!dzwjmcdata) return;
            this.electronfiledDialogVisible = false;
            this.formData.DZWJMC = dzwjmcdata;
        },
        //关闭电子文件名称dialog
        cancelElectronfile() {
            this.electronfiledDialogVisible = false;
        },
        save(param) {
            this.locFormVisible = false;
            this.multiPickupFormVisible = false;
            this.formData.YXZB = param;
            this.points=param;
        },
        savebtn(param) {
            this.pickupFormVisible = false;
            this.companylocVisible = false;
            this.formData.TGDWZB = param;
            this.point=param;
        },

        //附件地址添加、保存和取消
        addAddress() {
            this.fileDialogVis = true;
            this.fileaddressData = this.formData.LJ;
        },
        saveAddress(param) {
            this.fileDialogVis = false;
            this.formData.LJ = param;
        },
        cancelAddress() {
            this.fileDialogVis = false;
        },
        //查看时，确定和取消按钮不显示
        sureOrCancelVis() {
            let btndiv = document.getElementById('twoBtn');
            btndiv.style.display = "none";
        },
        // 上传缩略图
        handleAvatarSuccess(res, file) {
            // console.log(101, res);
            // console.log(102, file);
            this.imgPath = res;
            this.imageUrl = URL.createObjectURL(file.raw);
            // console.log(11111, this.imageUrl);
        },
        //坐标拾取
        toPickup() {
            if (this.state == EditState.READ)
                this.allowPickup = false;

            this.pickupFormVisible = true;
            
        },
        toMultiPickup() {
            this.multiPickupFormVisible = true;
            if (this.state == EditState.READ)
                this.allowPickup = false;

        },
    },

    created() {
        // let { id, name, state } = this.$route.query;
        this.initForm();
        this.companyLoc = this.formData.TGDWZB;
        this.mapCoordinateData = this.formData.YXZB;
        this.url = `${this.currentName}/${this.getNowFormatDate()}/`;

    },
    mounted() {
        this.getcompanyList();
        this.getlanguageData();
        this.getPeriodData();
        this.getRegionData();
        this.$el.getElementsByClassName('inline-input-vali')[0].querySelector("input").onblur = this.focusout;//获取提供单位名称组件的鼠标移开事件
        // if (this.state == "read") {
        //     this.sureOrCancelVis();
        // }
    }
}
</script>




